  // Sends action to Vuex that will log you in and redirect to the dash otherwise, error
  watch: {
    userBank (val) {
      if (this.userBank !== '') {
        this.userBankMessages = '✔'
      } else {
        this.userBankMessages = '✖ 請輸入您的銀行帳號所屬銀行名稱'
      }
    },
    userBankAccount (val) {
      if (this.userBankAccount !== '') {
        this.userBankAccountMessages = '✔'
      } else {
        this.userBankAccountMessages = '✖ 請輸入您的銀行帳號'
      }
    },
    userBankCode (val) {
      if (this.userBankCode !== '') {
        this.userBankCodeMessages = '✔'
        this.userBankNameList.forEach((b) => {
          if (b.value === val) {
            this.userBank = b.text
          }
        })
      } else {
        this.userBankCodeMessages = '✖ 請輸入您的銀行代碼'
      }
    },
    dologinView (val) {
      if (!val) {
        this.$router.push('/')
      }
    }
  },
  mounted () {
    if (localStorage.getItem('token')) {
      let body = {
        userId: (this.userInfo ? this.userInfo.user_id : -1),
        url: this.$route.path
      }
      this.$http.post('/api/account/authCheck/', qs.stringify(body)).then(res => res.data)
        .then(res => {
          if (res.status === 'success') {
            if (res.data.authority) {
              if (this.userInfo.type === 'teacher') {
                this.getExamBankOrderHistory()
                this.getLessonSalesHistory()
                this.getQPointsWtihdrawalHistory()
                this.getBankingInfomation()
              }
              if (this.userInfo.type === 'student') {
                this.getExamBankOrderHistory()
                this.getStudentOrderHistory()
                this.getQPointsWtihdrawalHistory()
                this.getBankingInfomation()
              }
            } else {
              this.$router.push('/')
            }
          } else if (localStorage.getItem('user')) {
            this.$store.dispatch('logout').then(() => {
              this.$router.go(0)
            })
          }
        })
        .catch(err => {
          console.log(err)
          if (localStorage.getItem('user')) {
            this.$store.dispatch('logout').then(() => {
              this.$router.go(0)
            })
          }
        })
    } else {
      // this.$router.push('/')
      this.$store.state.dologinView = true
    }
