  methods: {
    test () {
      alert('未開放')
    },
    getExamBankOrderHistory () {
      let body = {
        userId: (this.userInfo ? this.userInfo.user_id : -1),
        type: this.userInfo ? this.userInfo.type : ''
      }
      this.$http.post('/api/account_finance/examBankOrderHistory/', qs.stringify(body)).then(res => res.data)
        .then(res => {
          if (res.status === 'success') {
            this.questionBankInfo = res.data || {}
          } else if (res.status === 'failed') {
            this.snackbar = true
            this.color = 'red'
            this.errorMessages = res.errMsg
          } else {
            this.snackbar = true
            this.color = 'orange'
            this.errorMessages = '發生錯誤，請稍後再試'
          }
        })
        .catch(err => {
