      this.loadingQPWithdrawalStatus = true
      let body = {
        userId: (this.userInfo ? this.userInfo.user_id : -1),
        type: this.userInfo ? this.userInfo.type : ''
      }
      this.$http.post('/api/account_finance/getQPointsWithdrawalHistory/', qs.stringify(body)).then(res => res.data)
        .then(res => {
