    getBankingInfomation (type) {
      this.userBankCodeMessages = '請輸入您的銀行代碼'
      this.userBankMessages = '請輸入您的銀行帳號所屬銀行名稱'
      this.userBankAccountMessages = '請輸入您的銀行帳號'
      let body = {
        userId: (this.userInfo ? this.userInfo.user_id : -1),
        type: this.userInfo ? this.userInfo.type : ''
      }
      this.$http.post('/api/account/getBankingInfomation/', qs.stringify(body)).then(res => res.data)
        .then(res => {
          if (res.status === 'success') {
            this.userBankCode = res.data.bank_code
            this.userBank = res.data.bank_name
            this.userBankAccount = res.data.bank_account_code
            this.userBalance = res.data.balance
            this.userWithholdingBalance = res.data.withholding_balance
            this.userTxnFee = res.data.txn_fee
            this.$store.state.deposit = res.data.balance
          } else if (res.status === 'failed') {
            this.snackbar = true
            this.color = 'red'
            this.errorMessages = res.errMsg
          } else {
            this.snackbar = true
            this.color = 'orange'
            this.errorMessages = '發生錯誤，請稍後再試'
          }
        })
        .catch(err => {
          this.snackbar = true
          this.color = 'orange'
          this.errorMessages = '發生錯誤，請稍後再試'
          console.log(err)
        })
      if (type === 'only_editting') {
        this.userBankInfoView = true
      } else if (type === 'withdrawal_after_editting') {
        if (this.userBalance === 0) {
          alert('您目前尚無Q幣可提領')
        } else if ((this.userBalance - this.userTxnFee) > 0) {
          this.withdrawalView = true
        } else {
          alert('您目前剩餘Q幣不足扣除提領手續費')
        }
      }
    },
    getLessonReservationTime (lessonID, teacherID) {
      if (localStorage.getItem('token') && this.userInfo.type === 'student') {
        let body = {
          userId: (this.userInfo ? this.userInfo.user_id : -1),
          lessonID: lessonID
        }
        this.$http.post('/api/lesson/getStudentsAvailableRemainingMinutes/', qs.stringify(body)).then(res => res.data)
          .then(res => {
            if (res.status === 'success') {
              if (res.data.all_available_remaining_minutes_of_this_lesson > 0) {
                if (res.data.student_has_unused_trial_lesson_sales_set) {
                  alert('因您尚有「試課方案」未使用，故本次預約僅能先使用「試課方案」時數')
                }
                let reservation = {
                  teacherID: teacherID,
                  lessonID: lessonID,
                  time: res.data.student_has_unused_trial_lesson_sales_set ? 30 : res.data.all_available_remaining_minutes_of_this_lesson,
                  trial: res.data.student_has_unused_trial_lesson_sales_set
                }
                localStorage.setItem('reservation', JSON.stringify(reservation))
                this.$router.push('/lesson/appointment')
              } else {
                alert('目前您尚無可用的預約時數，請先購買課程方案')
              }
            } else if (res.status === 'failed') {
              this.snackbar = true
              this.color = 'red'
              this.errorMessages = res.errMsg
            } else {
              this.snackbar = true
              this.color = 'orange'
              this.errorMessages = '發生錯誤，請稍後再試'
            }
          })
          .catch(err => {
            this.snackbar = true
            this.color = 'orange'
            this.errorMessages = '發生錯誤，請稍後再試'
            console.log(err)
          })
      } else {
        alert('目前無法預約')
      }
    },
    openOrderBankInfo (val) {
      this.orderBankInfo = val.edony_bank_info
      this.orderBankInfoView = true
    },
    doOrderPayConfirm (val) {
      let isPayBankCode = /^[0-9]{5}$/
      if (isPayBankCode.test(val.pay_bank_code)) {
        this.loadingViewText = '申請對帳中'
        this.loadingView = true
        let body = {
          userId: (this.userInfo ? this.userInfo.user_id : -1),
          type: this.userInfo ? this.userInfo.type : '',
          purchase_recordID: val.purchase_recordID,
          status_update: 0,
          part_of_bank_account_code: val.pay_bank_code
        }
        this.$http.post('/api/account_finance/studentEditOrder/', qs.stringify(body)).then(res => res.data)
          .then(res => {
            this.loadingView = false
            if (res.status === 'success') {
              // this.snackbar = true
              // this.color = 'green'
              // this.errorMessages = '轉出帳號回傳成功'
              this.getStudentOrderHistory()
            } else if (res.status === 'failed') {
              this.snackbar = true
              this.color = 'red'
              this.errorMessages = res.errMsg
            } else {
              this.snackbar = true
              this.color = 'orange'
              this.errorMessages = '發生錯誤，請稍後再試'
            }
          })
          .catch(err => {
            this.loadingView = false
            this.snackbar = true
            this.color = 'orange'
            this.errorMessages = '發生錯誤，請稍後再試'
            console.log(err)
          })
      } else {
        this.snackbar = true
        this.color = 'red'
        this.errorMessages = '轉出帳號末五碼輸入錯誤'
      }
    },
    doQuestionBankOrderPayConfirm (val) {
      let isPayBankCode = /^[0-9]{5}$/
      if (isPayBankCode.test(val)) {
        this.loadingViewText = '申請對帳中'
        this.loadingView = true
        let body = {
          userId: (this.userInfo ? this.userInfo.user_id : -1),
          type: this.userInfo ? this.userInfo.type : '',
          // purchase_recordID: val.purchase_recordID,
          status_update: 0,
          part_of_bank_account_code: val
        }
        this.$http.post('/api/account_finance/examBankEditOrder/', qs.stringify(body)).then(res => res.data)
          .then(res => {
            this.loadingView = false
            if (res.status === 'success') {
              // this.snackbar = true
              // this.color = 'green'
              // this.errorMessages = '轉出帳號回傳成功'
              this.getExamBankOrderHistory()
            } else if (res.status === 'failed') {
              this.snackbar = true
              this.color = 'red'
              this.errorMessages = res.errMsg
            } else {
              this.snackbar = true
              this.color = 'orange'
              this.errorMessages = '發生錯誤，請稍後再試'
            }
          })
          .catch(err => {
            this.loadingView = false
            this.snackbar = true
            this.color = 'orange'
            this.errorMessages = '發生錯誤，請稍後再試'
            console.log(err)
          })
      } else {
        this.snackbar = true
        this.color = 'red'
        this.errorMessages = '轉出帳號末五碼輸入錯誤'
      }
    },
    doOrderCancel (id, action, text) {
      if (confirm('請問您是否確認要' + text + '？')) {
        this.loadingViewText = text + '中'
        this.loadingView = true
        let body = {
          userId: (this.userInfo ? this.userInfo.user_id : -1),
          type: this.userInfo ? this.userInfo.type : '',
          purchase_recordID: id,
          status_update: action,
          part_of_bank_account_code: ''
        }
        this.$http.post('/api/account_finance/studentEditOrder/', qs.stringify(body)).then(res => res.data)
          .then(res => {
            this.loadingView = false
            if (res.status === 'success') {
              this.snackbar = true
              this.color = 'green'
              this.errorMessages = '已' + text
              this.getStudentOrderHistory()
              this.getBankingInfomation()
            } else if (res.status === 'failed') {
              this.snackbar = true
              this.color = 'red'
              this.errorMessages = res.errMsg
            } else {
              this.snackbar = true
              this.color = 'orange'
              this.errorMessages = '發生錯誤，請稍後再試'
            }
          })
          .catch(err => {
            this.loadingView = false
            this.snackbar = true
            this.color = 'orange'
            this.errorMessages = '發生錯誤，請稍後再試'
            console.log(err)
          })
      }
    },
