      if (confirm('請問您是否確認要' + text + '？')) {
        this.loadingViewText = text + '中'
        this.loadingView = true
        let body = {
          userId: (this.userInfo ? this.userInfo.user_id : -1),
          type: this.userInfo ? this.userInfo.type : '',
          // purchase_recordID: id,
          status_update: action,
          part_of_bank_account_code: ''
        }
        this.$http.post('/api/account_finance/examBankEditOrder/', qs.stringify(body)).then(res => res.data)
          .then(res => {
            this.loadingView = false
            if (res.status === 'success') {
              this.snackbar = true
              this.color = 'green'
              this.errorMessages = '已' + text
              this.getExamBankOrderHistory()
            } else if (res.status === 'failed') {
              this.snackbar = true
              this.color = 'red'
              this.errorMessages = res.errMsg
            } else {
              this.snackbar = true
              this.color = 'orange'
              this.errorMessages = '發生錯誤，請稍後再試'
            }
          })
          .catch(err => {
