            this.snackbar = true
            this.color = 'orange'
            this.errorMessages = '發生錯誤，請稍後再試'
            console.log(err)
          })
      }
    },
    doSubmitVerify () {
      let error = false
      if (this.userBank === '') {
        this.userBankMessages = '✖ 請輸入您的銀行帳號所屬銀行名稱'
        error = true
      }
      if (this.userBankAccount === '') {
        this.userBankAccountMessages = '✖ 請輸入您的銀行帳號'
        error = true
      }
      if (this.userBankCode === '') {
        this.userBankCodeMessages = '✖ 請輸入您的銀行代碼'
        error = true
      }
      return error
    },
    doEditUserBankInfo () {
      if (!this.doSubmitVerify()) {
        this.userBankInfoView = false
        this.loadingViewText = '銀行帳戶更新中'
        this.loadingView = true
        let body = {
          userId: (this.userInfo ? this.userInfo.user_id : -1),
          type: this.userInfo ? this.userInfo.type : '',
          bank_code: this.userBankCode,
          bank_name: this.userBank,
          bank_account_code: this.userBankAccount,
          action: 'only_editting',
          amount: 0
        }
        this.$http.post('/api/account_finance/withdrawQPoints/', qs.stringify(body)).then(res => res.data)
          .then(res => {
            this.loadingView = false
            if (res.status === 'success') {
              this.snackbar = true
              this.color = 'green'
              this.errorMessages = '銀行帳戶更新成功'
            } else if (res.status === 'failed') {
              this.userBankInfoView = true
              this.snackbar = true
              this.color = 'red'
              this.errorMessages = res.errMsg
            } else {
              this.userBankInfoView = true
              this.snackbar = true
              this.color = 'orange'
              this.errorMessages = '發生錯誤，請稍後再試'
            }
          })
          .catch(err => {
            this.loadingView = false
            this.userBankInfoView = true
            this.snackbar = true
            this.color = 'orange'
            this.errorMessages = '發生錯誤，請稍後再試'
            console.log(err)
          })
      } else {
        this.snackbar = true
        this.color = 'red'
        this.errorMessages = '請修正錯誤欄位後再送出'
      }
    },
    doQPWithdrawal () {
      if (!this.doSubmitVerify()) {
        if (confirm('你確定要提領Q幣嗎?')) {
          if ((this.userBalance - this.userTxnFee) > 0) {
            this.withdrawalView = false
            this.loadingViewText = '申請提領中'
            this.loadingView = true
            let body = {
              userId: (this.userInfo ? this.userInfo.user_id : -1),
              type: this.userInfo ? this.userInfo.type : '',
              bank_code: this.userBankCode,
              bank_name: this.userBank,
              bank_account_code: this.userBankAccount,
              action: 'withdrawal_after_editting',
              amount: (this.userBalance - this.userTxnFee)
            }
            this.$http.post('/api/account_finance/withdrawQPoints/', qs.stringify(body)).then(res => res.data)
              .then(res => {
                this.loadingView = false
                if (res.status === 'success') {
                  this.snackbar = true
                  this.color = 'green'
                  this.errorMessages = '申請提領已送出，將於七個工作日內匯入您的銀行帳戶'
                  this.getQPointsWtihdrawalHistory()
                  this.getBankingInfomation()
                } else if (res.status === 'failed') {
                  this.withdrawalView = true
                  this.snackbar = true
                  this.color = 'red'
                  this.errorMessages = res.errMsg
                } else {
                  this.withdrawalView = true
                  this.snackbar = true
                  this.color = 'orange'
                  this.errorMessages = '發生錯誤，請稍後再試'
                }
              })
              .catch(err => {
                this.loadingView = false
                this.withdrawalView = true
                this.snackbar = true
                this.color = 'orange'
                this.errorMessages = '發生錯誤，請稍後再試'
                console.log(err)
              })
          } else {
            alert('您目前剩餘Q幣不足扣除提領手續費')
            this.withdrawalView = false
          }
        }
      } else {
        this.snackbar = true
        this.color = 'red'
        this.errorMessages = '請修正錯誤欄位後再送出'
      }
    }
  }
}
</script>
