      :src="loading"
      style="width: 2rem;margin-bottom: 10px;">
      <br>
      {{ loadingViewText }}
      </p>
    </v-dialog>
    <v-dialog
      v-model="successView"
      persistent
      >
      <p style="letter-spacing: 5px;text-align: center !important;color: #ffffff;background-color: #000000dd;padding: 15px;margin: auto;">
      <v-icon class="text-success h1 m-0">mdi-checkbox-marked-circle-outline</v-icon>
      <br>
      {{ successViewText }}
