<script>
import qs from 'qs'
// import boyIcon from '@/assets/img/boy_icon.jpg'
// import girlIcon from '@/assets/img/girl_icon.jpg'
import cardBg1 from '@/assets/img/lesson_card_bg1.jpg'
import cardBg2 from '@/assets/img/lesson_card_bg2.jpg'
import cardBg3 from '@/assets/img/lesson_card_bg3.jpg'
import QuestionBankProductImg from '@/assets/img/question_bank_product_img.jpg'
import loading from '@/assets/img/loading.gif'
import { mapState } from 'vuex'
export default {
  name: 'Checkout',
  metaInfo () {
    return {
      title: this.appName + '｜結帳'
    }
  },
  data: function () {
    return {
      loadingViewText: '處理中',
      loadingView: false,
      loadingArray: [],
      successViewText: '處理中',
      successView: false,
      pageView: false,
      orderStatus: false,
      // boyIcon,
      // girlIcon,
      cardBg1,
      cardBg2,
      cardBg3,
      QuestionBankProductImg,
      loading,
      snackbar: false,
      color: 'red',
      errorMessages: '',
      // productList: {
      //   teacherId: -1,
      //   teacherNickname: '',
      //   lessonId: '',
      //   lessonCardBgPath: '',
      //   lessonName: '',
      //   lessonFee: 0,
      //   lessonSaleTry: false,
      //   lessonSaleFeeTry: 200,
      //   lessonSaleUnit: false,
      //   lessonSale: [],
      //   lessonSaleCase: ''
      // },
      productList: []
    }
  },
  computed: {
    ...mapState(['root', 'appName', 'cartUpdateTime', 'cartItem', 'isProduction', 'dologinView']),
    userInfo () {
      return JSON.parse(localStorage.getItem('user'))
    },
    shoppingInfo () {
      return JSON.parse(localStorage.getItem('shopping'))
    },
    total () {
      let total = 0
      this.productList.forEach((item, index) => {
        total += item.itemTotal
      })
      return total
    },
    cartItemHasTicy () {
      let answer = false
      this.productList.forEach((item, index) => {
        if (item.type === 'exam_bank_order') {
          answer = true
        }
      })
      return answer
    }
  },
  watch: {
    cartItem (val) {
      if (val <= 0 && !this.orderStatus) {
        this.snackbar = true
        this.color = 'red'
        this.errorMessages = '購物車內已無商品'
        setTimeout(() => {
          this.$router.push('/lesson/search')
        }, 3000)
      }
    },
    dologinView (val) {
      if (!val) {
        this.$router.push('/')
      }
    }
    // shoppingInfo: {
    //   handler: function (val, oldVal) { alert('c'); this.$router.go(0) },
    //   deep: true
    // }
    // 'shoppingInfo.updateTime': function (newValue) {
    //   this.$router.go(0)
    // }
  //   element () {
  //     this.getAverageRGB()
  //   }
  },
  // Sends action to Vuex that will log you in and redirect to the dash otherwise, error
  mounted () {
    if (localStorage.getItem('token')) {
      let body = {
        userId: (this.userInfo ? this.userInfo.user_id : -1),
        url: this.$route.path
      }
      this.$http.post('/api/account/authCheck/', qs.stringify(body)).then(res => res.data)
        .then(res => {
          if (res.status === 'success') {
            if (res.data.authority) {
              if (this.cartItem > 0) {
                this.loadingViewText = '訂單資訊更新中'
                this.loadingView = true
                this.loadingArray = []
                this.shoppingInfo.item.forEach((item, index) => {
                  if (item.order_type === 'lesson_order') {
                    this.loadingArray.push(true)
                    let array = {
                      type: 'lesson_order',
                      teacherId: -1,
                      teacherNickname: '',
                      lessonId: '',
                      lessonCardBgPath: '',
                      lessonName: '',
                      lessonFee: 0,
                      lessonSaleTry: false,
                      lessonSaleFeeTry: 200,
                      lessonSaleUnit: false,
                      lessonSale: [],
                      lessonSaleCase: '',
                      itemTotal: 0,
                      qDiscount: 0,
                      codeDiscount: 0,
                      code: ''
                    }
                    this.productList.push(array)
                    this.productList[index].type = item.order_type
                    this.productList[index].lessonSaleCase = item.sales_set
                    this.productList[index].itemTotal = item.total_amount_of_the_sales_set
                    this.productList[index].qDiscount = item.q_discount
                    this.productList[index].codeDiscount = item.code_discount
                    this.productList[index].code = item.code
                    // this.total = this.total + item.lessonSalePrice
                    // this.getLessonInfo(index)
                    this.getLessonInfo(index, item.lessonID)
                  } else if (item.order_type === 'exam_bank_order') {
                    this.loadingArray.push(false)
                    let array = {
                      type: 'exam_bank_order',
                      productName: '智慧題庫(預)',
                      productBgPath: this.QuestionBankProductImg,
                      productFee: 0,
                      productSale: [{
                        text: '早鳥預購方案',
                        day: '365',
                        off: '19'
                      }],
                      productSaleCase: 0,
                      itemTotal: 0,
                      qDiscount: 0,
                      codeDiscount: 0,
                      code: ''
                    }
                    this.productList.push(array)
                    this.productList[index].type = item.order_type
                    // this.productList[index].productSaleCase = item.sales_set
                    this.productList[index].productFee = item.total_amount_of_the_sales_set
                    this.productList[index].itemTotal = Math.round(item.total_amount_of_the_sales_set * 19 / 100)
                    this.productList[index].qDiscount = item.q_discount
                    this.productList[index].codeDiscount = item.code_discount
                    this.productList[index].code = item.code
                    if (this.shoppingInfo.item.length === 1) {
                      this.pageView = true
                      this.loadingView = false
                    }
                  }
                })
              } else {
                this.snackbar = true
                this.color = 'red'
                this.errorMessages = '您尚未選購商品'
                setTimeout(() => {
                  this.$router.push('/lesson/search')
                }, 1000)
              }
            } else {
              this.$router.push('/')
            }
          } else if (localStorage.getItem('user')) {
            this.$store.dispatch('logout').then(() => {
              this.$router.go(0)
            })
          }
        })
        .catch(err => {
          console.log(err)
          // if (localStorage.getItem('user')) {
          //   this.$store.dispatch('logout').then(() => {
          //     this.$router.go(0)
          //   })
          // }
        })
    } else if (this.cartItem > 0) {
      this.$store.state.dologinView = true
    } else {
      this.$router.push('/')
    }
  },
  methods: {
    test () {
      alert('未開放')
    },
    doGtagsEvent (name, description, remarks) {
      if (this.isProduction) {
        this.$gtag.event(name, {
          description: description,
          remarks: remarks,
          page_path: this.$route.fullPath,
          hostname: this.root,
          user_id: this.userInfo ? this.userInfo.user_id : -1
        })
      }
      // this.$gtag.event('testlinker', {
      //   'event_category': 't1',
      //   'event_label': 't2',
      //   'value': true
      // })
    },
    // getTeacherProfile (id) {
    //   this.$http.get(`/api/account/returnTeacherProfileForPublicViewing/?userID=${id}&type=teacher`).then(res => res.data)
    //     .then(res => {
    //       if (res.status === 'success') {
    //         this.pageView = true
    //         this.teacherApproved = res.data.id_approved
    //         // this.teacherAvgScore = res.data.all_lesson_score_mean
    //         // this.teacherReviewedTimes = res.data.total_number_of_remark
    //         this.teacherEdu1 = res.data.education_1
    //         this.teacherEdu2 = res.data.education_2
    //         this.teacherEdu3 = res.data.education_3
    //         this.teacherEduApproved = res.data.education_approved
    //         this.teacherJob = res.data.company
    //         this.teacherJobApproved = res.data.work_approved
    //         this.teacherExp = res.data.special_exp
    //         this.teacherExpApproved = res.data.other_approved
    //         this.teacherAllApproved = res.data.is_approved
    //         this.teacherNickname = res.data.nickname
    //         this.teacherYear = res.data.tutor_experience
    //         // this.teacherIntro = res.data.intro
    //         // this.teacherTag = res.data.subject_type
    //         this.teacherGender = res.data.is_male ? boyIcon : girlIcon
    //         if (res.data.thumbnail_dir !== '') {
    //           this.teacherPhoto = res.data.thumbnail_dir
    //         } else {
    //           if (res.data.is_male) {
    //             this.teacherPhoto = this.boyTeacher
    //           } else {
    //             this.teacherPhoto = this.girlTeacher
    //           }
    //         }
    //         this.lessonDateDefault = res.data.available_time || []
    //         this.lessonDateBooked = res.data.booked_time || []
    //       } else {
    //         this.$router.go(-1)
    //       }
    //     })
    // },
    getLessonInfo (index, id) {
      this.$http.get(`/api/lesson/returnLessonDetailsForBrowsing/?action=browsing&lessonID=${id}&userID=${this.userInfo ? this.userInfo.user_id : -1}`).then(res => res.data)
        .then(res => {
          this.loadingArray[index] = false
          if (res.status === 'success') {
            this.pageView = true
            this.productList[index].lessonCardBg = res.data.background_picture_code
            if (res.data.background_picture_code === 99) {
              this.productList[index].lessonCardBgPath = res.data.background_picture_path
            } else if (res.data.background_picture_code === 0) {
              this.productList[index].lessonCardBgPath = this.cardBg1
            } else if (res.data.background_picture_code === 1) {
              this.productList[index].lessonCardBgPath = this.cardBg2
            } else if (res.data.background_picture_code === 2) {
              this.productList[index].lessonCardBgPath = this.cardBg3
            }
            this.productList[index].teacherId = res.data.teacher_auth_id
            this.productList[index].teacherNickname = res.data.teacher_nickname
            this.productList[index].lessonName = res.data.lesson_title // 課程名稱
            this.productList[index].lessonFee = res.data.price_per_hour // 鐘點費
            this.productList[index].lessonSaleTry = (Number(res.data.trial_class_price) >= 0) // 試課費用
            this.productList[index].lessonSaleFeeTry = (Number(res.data.trial_class_price) >= 0) ? Number(res.data.trial_class_price) : 200
            // this.productList[index].lessonSaleUnit = (Number(res.data.unitClassPrice) > 0)
            this.productList[index].lessonSaleUnit = res.data.lesson_has_one_hour_package // 單節方案
            if (res.data.discount_price !== '' && res.data.discount_price !== null) {
              let discountPrice = res.data.discount_price.split(';')
              this.productList[index].lessonSale = []
              discountPrice.forEach((d, i) => {
                if (d !== '') {
                  let dd = d.split(':')
                  this.productList[index].lessonSale.push({ hour: dd[0], off: dd[1] })
                }
              })
            }
          } else if (res.status === 'failed') {
            // this.snackbar = true
            // this.color = 'red'
            // this.errorMessages = res.errMsg
            this.$router.go(-1)
          } else {
            // this.snackbar = true
            // this.color = 'orange'
            // this.errorMessages = '發生錯誤，請稍後再試'
            this.$router.go(-1)
          }
          if (this.loadingArray.indexOf(true) === -1) {
            this.loadingView = false
          }
        })
        .catch(err => {
          // this.loadingView = false
          // this.snackbar = true
          // this.color = 'orange'
          // this.errorMessages = '發生錯誤，請稍後再試'
          console.log(err)
          this.$router.go(-1)
        })
    },
    doChangeCase (type, index, salesSet, price, promotionCode) {
      if (type === 'lesson_order') {
        let shopping = JSON.parse(localStorage.getItem('shopping'))
        shopping.updateTime = new Date().getTime()
        shopping.item[index].sales_set = salesSet
        shopping.item[index].total_amount_of_the_sales_set = price
        shopping.item[index].code = promotionCode
        localStorage.setItem('shopping', JSON.stringify(shopping))
        this.$store.state.cartUpdateTime = shopping.updateTime
        this.productList[index].lessonSaleCase = salesSet
        this.productList[index].itemTotal = price
        this.productList[index].code = promotionCode
      }
    },
    copyItem (index) {
      let shopping = JSON.parse(localStorage.getItem('shopping'))
      shopping.updateTime = new Date().getTime()
      let newShoppingItem = JSON.stringify(shopping.item[index])
      shopping.item.splice((index + 1), 0, JSON.parse(newShoppingItem))
      let newProductListItem = JSON.stringify(this.productList[index])
      this.productList.splice((index + 1), 0, JSON.parse(newProductListItem))
      localStorage.setItem('shopping', JSON.stringify(shopping))
      this.$store.state.cartItem = shopping.item.length
      this.$store.state.cartUpdateTime = shopping.updateTime
      this.snackbar = true
      this.color = 'green'
      this.errorMessages = '購物車已更新'
    },
    removeItem (index) {
      let shopping = JSON.parse(localStorage.getItem('shopping'))
      shopping.updateTime = new Date().getTime()
      shopping.item.splice(index, 1)
      this.productList.splice(index, 1)
      localStorage.setItem('shopping', JSON.stringify(shopping))
      this.$store.state.cartItem = shopping.item.length
      this.$store.state.cartUpdateTime = shopping.updateTime
      this.snackbar = true
      this.color = 'green'
      this.errorMessages = '購物車已更新'
    },
    doSubmit () {
      // if (this.cartItem === 1 && this.shoppingInfo.item[0].order_type === 'lesson_order') {
      if (this.cartItem >= 1) {
        this.loadingViewText = '訂單處理中'
        this.loadingView = true
        let formData = new FormData()
        // formData.append('order_type', this.teacherId)
        // formData.append('teacherID', this.teacherId)
        // formData.append('lessonID', this.shoppingInfo.item[0].lessonID)
        formData.append('userID', this.userInfo ? this.userInfo.user_id : -1)
        // formData.append('sales_set', Number.isInteger(this.lessonSaleCase) ? (this.lessonSale[this.lessonSaleCase].hour + ':' + this.lessonSale[this.lessonSaleCase].off) : this.lessonSaleCase)
        formData.append('total_amount_of_orders', this.total)
        formData.append('total_q_discount', 0)
        // formData.append('total_order', JSON.stringify(this.shoppingInfo.item))
        formData.append('total_order', JSON.stringify(JSON.parse(localStorage.getItem('shopping')).item))
        this.$http.post('api/account_finance/storageOrder/', formData).then(res => res.data)
          .then(res => {
            this.loadingView = false
            if (res.status === 'success') {
              if (this.cartItemHasTicy) {
                window.fbq('trackCustom', 'checkoutTicy', { user_id: (this.userInfo ? this.userInfo.user_id : -1), dev: !this.isProduction })
              }
              if (this.isProduction) {
                this.doGtagsEvent('Checkout-按鈕：結帳', '按下[結帳]按鈕', formData)
              }
              this.successViewText = '訂單已成立，即將自動前往帳務中心'
              this.successView = true
              this.orderStatus = true
              // this.productList = []
              let shopping = {
                updateTime: new Date().getTime(),
                item: []
              }
              localStorage.setItem('shopping', JSON.stringify(shopping))
              this.$store.state.cartItem = 0
              this.$store.state.cartUpdateTime = shopping.updateTime
              // this.snackbar = true
              // this.color = 'green'
              // this.errorMessages = '訂單已成立，即將自動前往帳務中心'
              setTimeout(() => {
                this.$router.push('/account/finance')
              }, 3000)
            } else if (res.status === 'failed') {
              this.snackbar = true
              this.color = 'red'
              this.errorMessages = res.errMsg
            } else {
              this.snackbar = true
              this.color = 'orange'
              this.errorMessages = '發生錯誤，請稍後再試'
            }
          })
          .catch(err => {
            this.snackbar = true
            this.color = 'orange'
            this.errorMessages = '發生錯誤，請稍後再試'
            console.log(err)
          })
      } else {
        this.snackbar = true
        this.color = 'red'
        this.errorMessages = '目前尚不支援多元購物車結帳'
      }
    }
  }
}
</script>
