<template>
  <div id="chatroom">
    <div v-if="doChatAction==='list'">
      <div
        :class="{'system':(userInfo?userInfo.type:'')==='system'}"
        class="topbox row">
        <span class="col-12 text-center">Message</span>
      </div>
      <div class="middlebox">
        <div
          v-if="updateStatus"
          class="loading"/>
        <div class="list-area">
          <div
            v-for="(room, r) in chatroom"
            :key="'r'+r"
            :class="{ 'new': room.chatUnreadMessageQty>0 }"
            class="list cursor-no-hover"
            @click="chatroomIndex=r;chatroomChatroomID=room.chatroomID;$store.state.doChatAction='message';inChatroom(r)">
            <div
              :style="'background-image:url('+((room.chatUserPath !== '') ? (root + room.chatUserPath) : (room.chatUsergender?(room.chatUserType==='teacher'?boyTeacher:boyStudent):(room.chatUserType==='teacher'?girlTeacher:girlStudent)))+');'"
              class="photo"/>
            <span class="name">{{ room.chatUserName }}{{ ((userInfo?userInfo.type:'')!=='system' && room.chatroom_type==='system2user')?'🔰':(userInfo?userInfo.type:'')==='system'?getUserIcon(room.chatUserType,room.chatUsergender):'' }}</span>
            <span class="time">{{ (room.messageInfo.length>0)?(room.messageInfo[(room.messageInfo.length-1)].messageStatus!=='temp'?($moment(room.messageInfo[(room.messageInfo.length-1)].messageCreateTime).format('HH:mm')):'傳送中'):'' }}</span>
            <p>{{ (room.messageInfo.length>0)?messagePreviewTextReplace(room.messageInfo[(room.messageInfo.length-1)].messageText):'' }}</p>
          </div>
        </div>
      </div>
    </div>
    <div v-if="doChatAction==='message'">
      <div
        :class="{'system':(userInfo?userInfo.type:'')==='system'}"
        class="topbox row">
        <v-icon
          class="col-1 cursor-no-hover"
          @click="outChatroom(chatroomIndex);$store.state.doChatAction='list'">mdi-chevron-left</v-icon>
        <span class="col-10 text-center">
          <div
            :style="'background-image:url('+((chatroom[chatroomIndex].chatUserPath !== '') ? (root + chatroom[chatroomIndex].chatUserPath) : (chatroom[chatroomIndex].chatUsergender?(chatroom[chatroomIndex].chatUserType==='teacher'?boyTeacher:boyStudent):(chatroom[chatroomIndex].chatUserType==='teacher'?girlTeacher:girlStudent)))+');'"
            class="photo"/>{{ chatroom[chatroomIndex].chatUserName }}{{ ((userInfo?userInfo.type:'')!=='system' && chatroom[chatroomIndex].chatroom_type==='system2user')?'🔰':(userInfo?userInfo.type:'')==='system'?getUserIcon(chatroom[chatroomIndex].chatUserType,chatroom[chatroomIndex].chatUsergender):'' }}
        </span>
        <!-- <v-icon
          v-if="userInfo.type!=='teacher'"
          class="col-1 text-white cursor-no-hover"
          @click="$store.state.doChatAction='info'">mdi-alert-circle-outline</v-icon> -->
        <v-icon
          v-if="userInfo.type!=='teacher'"
          class="col-1 text-white cursor-no-hover"
          @click="$router.history.current.path===('/account/profile/'+(chatroom[chatroomIndex].chatUserID||''))?'':$router.push('/account/profile/'+(chatroom[chatroomIndex].chatUserID||''))">mdi-information-outline</v-icon>
        <v-icon
          v-if="userInfo.type==='teacher'"
          class="col-1 text-white cursor-no-hover"/>
      </div>
      <!-- <div class="booking cursor-no-hover">
        <v-icon class="text-white left">mdi-chevron-double-right</v-icon>立即預約<v-icon class="text-white right">mdi-chevron-double-left</v-icon>
      </div> -->
      <!-- <div
        id="messagebox"
        :class="{'bg-booking':(!loadingStatus&&chatroom[chatroomIndex].chatUserLessonList!==[]&&userInfo.type!=='teacher')}"
        class="middlebox bg-message"> -->
      <div
        id="messagebox"
        class="middlebox bg-message">
        <div
          v-for="(message, m) in chatroom[chatroomIndex].messageInfo"
          :key="'m'+m"
          :class="{'our': (message.messageType!=='auto_system_msg')&&(message.senderID===userInfo.user_id), 'other': (message.messageType!=='auto_system_msg')&&(message.senderID!==userInfo.user_id), 'system':message.messageType==='auto_system_msg', 'booked':message.messageType==='booking_link_msg'}"
          class="message">
          <span class="time">{{ message.messageStatus!=='temp'?$moment(message.messageCreateTime).format('HH:mm'):'傳送中' }}</span>
          <p v-if="message.messageType==='text_msg'"><span v-html="messageTextReplace(message.messageText)"></span></p>
          <p v-if="message.messageType==='auto_system_msg'"><span>{{ message.messageStatus!=='temp'?$moment(message.messageCreateTime).format('HH:mm'):'傳送中' }}</span><br><span v-html="messageTextReplace(message.messageText)"></span></p>
          <div v-if="message.messageType==='booking_link_msg'">
            <p class="subtitle"><v-icon class="text-white mr-1">mdi-clock-alert-outline</v-icon>預約資訊</p>
            <p class="title">{{ JSON.parse(message.messageText).lesson_title }}</p>
            <p><span>預約日期</span>{{ $moment(JSON.parse(message.messageText).bookingDate).format('YYYY年MM月DD日') }}</p>
            <p><span>預約時段</span>{{ JSON.parse(message.messageText).bookingTime }}</p>
            <v-btn
              dark
              flat
              class="m-0 mt-2 w-100 border border-white bg-select-color"
              @click="$router.push((userInfo.type==='teacher')?'/account/reservation':'/account/study')">
              {{ '前往' + (userInfo.type==='teacher')?'預約管理':'學習歷程' + '確認' }}
            </v-btn>
          </div>
          <span class="time">{{ message.messageStatus!=='temp'?$moment(message.messageCreateTime).format('HH:mm'):'傳送中' }}</span>
        </div>
      </div>
      <div
        :class="{'system':(userInfo?userInfo.type:'')==='system'}"
        class="bottombox">
        <!-- <p style="position: absolute;top: -3px;">{{ chatroom[chatroomIndex].websock.url }}</p> -->
        <textarea
          v-model="messageText"
          placeholder="在此輸入訊息"
          @keydown.enter="chatroom[chatroomIndex].chatroom_type==='system2user'?websocketsendSystem():websocketsend()">
        </textarea>
        <v-icon
          class="text-white mr-1 cursor"
          @click="chatroom[chatroomIndex].chatroom_type==='system2user'?websocketsendSystem():websocketsend()">mdi-send</v-icon>
      </div>
    </div>
    <div v-if="doChatAction==='info'">
      <div
        :class="{'system':(userInfo?userInfo.type:'')==='system'}"
        class="topbox row">
        <v-icon
          class="col-1 cursor-no-hover"
          @click="$store.state.doChatAction='message'">mdi-chevron-left</v-icon>
        <span
          v-if="doChatAction==='info'"
          class="col-10 text-center">教師資訊</span>
        <div class="col-1"/>
      </div>
      <div class="middlebox">
        <div class="profile">
          <div
            :style="'background-image:url('+((chatroom[chatroomIndex].chatUserPath !== '') ? (root + chatroom[chatroomIndex].chatUserPath) : (chatroom[chatroomIndex].chatUsergender?(chatroom[chatroomIndex].chatUserType==='teacher'?boyTeacher:boyStudent):(chatroom[chatroomIndex].chatUserType==='teacher'?girlTeacher:girlStudent)))+');'"
            class="photo"/>
          <p class="h4 subtitle mb-2">關於老師</p>
          <p class="content">{{ chatroom[chatroomIndex].chatUserIntro }}</p>
          <p class="h4 subtitle mb-2">開設課程</p>
          <div
            :class="{'loading':loadingStatus&&!chatroom[chatroomIndex].chatUserLessonList}"
            class="lesson">
            <p
              v-if="!loadingStatus&&chatroom[chatroomIndex].chatUserLessonList===[]"
              class="text-center w-100">尚未開設課程</p>
            <v-btn
              v-for="(lesson, l) in chatroom[chatroomIndex].chatUserLessonList"
              :key="'l'+l"
              dark
              flat
              @click="$router.push('/lesson/main/view/'+lesson.value)"
            >
              {{ lesson.text }}
            </v-btn>
          </div>
          <v-btn
            v-if="chatroom[chatroomIndex].chatUserLessonList&&chatroom[chatroomIndex].chatUserLessonList!==[]"
            dark
            flat
          >
            立即預約
          </v-btn>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import qs from 'qs'
import boyTeacher from '@/assets/img/boy_teacher.jpg'
import girlTeacher from '@/assets/img/girl_teacher.jpg'
import boyStudent from '@/assets/img/boy_student.jpg'
import girlStudent from '@/assets/img/girl_student.jpg'
import loading from '@/assets/img/loading.gif'
import { mapState } from 'vuex'
export default {
  name: 'Chatroom',
  // props: {
  //   readonly: {
  //     type: Boolean,
  //     default: false
  //   },
  //   selectTimeDefault: {
  //     type: String,
  //     default: null
  //   },
  //   selectDateDefault: {
  //     type: Array,
  //     default: null
  //   }
  // },
  data: () => ({
    boyTeacher,
    girlTeacher,
    boyStudent,
    girlStudent,
    loading,
    loadingStatus: false,
    updateStatus: false,
    // websock: [],
    websockSystem: [],
    chatroom: [],
    chatroomIndex: -1,
    chatroomChatroomID: 0,
    messageText: ''
  }),
  // created () {
  //   this.initWebSocket()
  // },
  // destroyed () {
  //   this.websock.close()
  // },
  computed: {
    ...mapState(['root', 'appName', 'loginStatus', 'doChatView', 'doChatAction', 'doChatroomID', 'doReadStatus']),
    userInfo () {
      return JSON.parse(localStorage.getItem('user'))
    },
    chatUnreadMessageQty () {
      let qty = 0
      this.chatroom.forEach((room, index) => {
        qty = qty + room.chatUnreadMessageQty
      })
      return qty
    }
  },
  watch: {
    loginStatus (val) {
      // if (val) {
      //   this.getHistoricalMessage()
      //   this.initWebSocket()
      // } else {
      //   alert('已手動斷線')
      //   this.websock.close()
      //   this.$router.go(0)
      // }
    },
    doChatView (val) {
      if (val) {
        this.doChatroomMessageRead(this.chatroomIndex)
        this.doOpenChatWindows()
      } else {
        this.doCloseChatWindows()
      }
    },
    doChatAction (val) {
      if (val === 'list') {
        this.chatroomIndex = -1
      } else if (val === 'message') {
        setTimeout(() => {
          document.getElementById('messagebox').scroll(0, document.getElementById('messagebox').scrollHeight)
        }, 50)
      }
    },
    doChatroomID (id) {
      // if (id >= 0) {
      if (id !== -1) {
        let open = false
        this.chatroom.forEach((room, index) => {
          if (room.chatroomID === id) {
            this.chatroomIndex = index
            this.chatroomChatroomID = id
            this.$store.state.doChatAction = 'message'
            this.$emit('snackbar', ['green', '已進入與使用者' + this.chatroom[index].chatUserID + '的' + this.chatroom[index].chatroomID + '號聊天室'])
            open = true
            this.updateStatus = false
            this.$store.state.doChatroomID = -1
          }
        })
        if (!open) {
          this.doCreateChatroom(id)
        }
      }
    },
    chatUnreadMessageQty (val) {
      this.$store.state.doReadStatus = (val > 0)
    },
    chatroomIndex (val) {
      if (val >= 0) {
        // this.chatroom[val].chatUnreadMessageQty = 0
        this.doChatroomMessageRead(val)
      }
      this.infoIntro = ''
      this.infoLessonList = []
    }
  },
  // // ready () {
  // //   window.addEventListener('focus', this.handleWinFocus)
  // // },
  // // beforeDestroy () {
  // //   window.removeEventListener('focus', this.handleWinFocus)
  // // },
  // destroyed: () => {
  //   window.removeEventListener('focus', this.handleWinFocus)
  // },
  mounted () {
    // window.addEventListener('focus', this.handleWinFocus)
    // // window.onfocus = this.startTimer;
    // // window.onblur = this.pauseTimer;
    // // setInterval(() => alert('QQ'), 5000);
    if (localStorage.getItem('token')) {
      // setInterval(() => {
      //   this.$store.state.appName = (this.chatUnreadMessageQty > 0) ? ((this.appName.indexOf('新訊息') === -1) ? (this.chatUnreadMessageQty + '則新訊息🔔🔔🔔開課！Quikok') : '開課！Quikok') : '開課！Quikok'
      // }, 1000)
      this.websock = []
      this.websockSystem = []
      // this.initSystemWebSocket()
      this.getSystemHistoricalMessage()
      // this.getHistoricalMessage()
      // if (this.userInfo.type === 'teacher') {
      //   setInterval(() => {
      //     // this.chatroom.forEach((room, index) => {
      //     //   this.websock[index].close()
      //     // })
      //     this.doNewChatroomConnect('老師')
      //   }, 1000 * 60 * 5)
      // }
    } else {
      this.chatroom.forEach((room, index) => {
        // this.websock[index].close()
        this.chatroom[index].websock.close()
      })
      this.$emit('snackbar', ['red', '已手動斷線'])
    }
  },
  methods: {
    // handleWinFocus () {
    //   if (this.chatUnreadMessageQty > 0) {
    //     alert('🔔您有' + this.chatUnreadMessageQty + '則新訊息未讀唷～🔔')
    //   }
    // },
    getUserIcon (type, gender) {
      if (type === 'teacher') {
        if (gender) {
          return '👨‍🏫'
        } else {
          return '👩‍🏫'
        }
      } else if (type === 'student') {
        if (gender) {
          return '👨‍🎓'
        } else {
          return '👩‍🎓'
        }
      }
    },
    messagePreviewTextReplace (text) {
      return text.replace(/\n/g, ' ').replace(/\[\(\S*\)\[/g, '').replace(/\]\]/g, '')
    },
    messageTextReplace (text) {
      return text.replace(/\n/g, '<br>').replace(/\[\(/g, '<a href="').replace(/\)\[/g, '">').replace(/\]\]/g, '</a>')
    },
    getTeacherProfile (id) {
      this.chatroom.forEach((room, index) => {
        this.$emit('snackbar', ['blue', '準備取得老師(ID:' + room.chatUserID + ')資料' + (id || '')])
        if (!id || (id === room.chatroomID)) {
          this.$emit('snackbar', ['blue', '取得老師(ID:' + room.chatUserID + ')資料中' + (id || '')])
          this.$http.get(`/api/account/returnTeacherProfileForPublicViewing/?userID=${room.chatUserID}&type=teacher`).then(res => res.data)
            .then(res => {
              if (res.status === 'success') {
                this.$emit('snackbar', ['green', '已取得老師(ID:' + room.chatUserID + ')資料' + (id || '')])
                this.chatroom[index].chatUserIntro = res.data.intro
              } else if (res.status === 'failed') {
                // this.snackbar = true
                // this.color = 'red'
                // this.errorMessages = res.errMsg
                this.$emit('snackbar', ['red', 'error:' + res.errMsg])
              } else {
                // this.snackbar = true
                // this.color = 'orange'
                // this.errorMessages = '發生錯誤，請稍後再試'
                this.$emit('snackbar', ['orange', '發生錯誤，請稍後再試'])
              }
            })
            .catch(err => {
              // this.snackbar = true
              // this.color = 'orange'
              // this.errorMessages = '發生錯誤，請稍後再試'
              this.$emit('snackbar', ['orange', '發生錯誤，請稍後再試'])
              console.log(err)
            })
        }
      })
    },
    getLessonList (id) {
      this.loadingStatus = true
      this.chatroom.forEach((room, index) => {
        this.$emit('snackbar', ['blue', '準備取得老師(ID:' + room.chatUserID + ')課程清單' + (id || '')])
        if (!id || (id === room.chatroomID)) {
          this.$emit('snackbar', ['blue', '取得老師(ID:' + room.chatUserID + ')課程清單中' + (id || '')])
          this.infoLessonList = []
          let body = {
            userId: (this.userInfo ? this.userInfo.user_id : -1),
            qty: -1,
            keywords: '',
            only_show_ones_favorites: false,
            only_show_lessons_by_this_teacher_s_auth_id: room.chatUserID,
            ordered_by: '',
            filtered_by: ''
          }
          this.$http.post('/api/lesson/getLessonCardsForCommonUsers/', qs.stringify(body)).then(res => res.data)
            .then(res => {
              this.loadingStatus = false
              if (res.status === 'success') {
                this.$emit('snackbar', ['green', '已取得老師(ID:' + room.chatUserID + ')課程清單' + (id || '')])
                this.chatroom[index].chatUserLessonList = []
                res.data.forEach((lesson) => {
                  this.chatroom[index].chatUserLessonList.push({ value: lesson.lessonID, text: lesson.lesson_title })
                })
              } else if (res.status === 'failed') {
                // this.snackbar = true
                // this.color = 'red'
                // this.errorMessages = res.errMsg
                this.$emit('snackbar', ['red', 'error:' + res.errMsg])
              } else {
                // this.snackbar = true
                // this.color = 'orange'
                // this.errorMessages = '發生錯誤，請稍後再試'
                this.$emit('snackbar', ['orange', '發生錯誤，請稍後再試'])
              }
            })
            .catch(err => {
              // this.snackbar = true
              // this.color = 'orange'
              // this.errorMessages = '發生錯誤，請稍後再試'
              this.$emit('snackbar', ['orange', '發生錯誤，請稍後再試'])
              console.log(err)
            })
        }
      })
    },
    doNewChatroomConnect (type) {
      this.$emit('snackbar', ['blue', '準備取得歷史訊息(' + type + ')'])
      if (localStorage.getItem('token')) {
        let chatroomTemp = []
        let chatroomList = []
        this.chatroom.forEach((room, index) => {
          chatroomList.push(room.chatroomID)
        })
        this.$emit('snackbar', ['blue', '取得歷史訊息中(' + type + ')'])
        let body = {
          userId: (this.userInfo ? this.userInfo.user_id : -1),
          user_type: this.userInfo.type
        }
        this.$http.post('/api/chat/contentAndHistoryOfChatroom/', qs.stringify(body)).then(res => res.data)
          .then(res => {
            if (res.status === 'success') {
              this.$emit('snackbar', ['green', '已取得歷史訊息(' + type + ')'])
              chatroomTemp = res.data
              chatroomTemp.forEach((room, index) => {
                if (chatroomList.indexOf(room.chatroomID) === -1) {
                  this.chatroom.push(chatroomTemp[index])
                  setTimeout(() => {
                    this.doChatroomListSort()
                  }, 0)
                  setTimeout(() => {
                    this.$emit('snackbar', ['blue', '準備與' + room.chatroomID + '號聊天室連線(' + type + ')'])
                    const wsuri = this.$ws + `/ws/chat/${this.userInfo ? this.userInfo.user_id : -1}_chatroom_${chatroomTemp[index].chatroomID}_0/`
                    // this.websock.push(new WebSocket(wsuri))
                    // this.websock[this.websock.length - 1].onmessage = this.websocketonmessage
                    // this.websock[this.websock.length - 1].onopen = this.websocketonopen(chatroomTemp[index].chatUserID, chatroomTemp[index].chatroomID)
                    // this.websock[this.websock.length - 1].onerror = this.websocketonerror
                    // this.websock[this.websock.length - 1].onclose = this.websocketclose
                    this.chatroom[this.chatroom.length - 1].websock = new WebSocket(wsuri)
                    this.chatroom[this.chatroom.length - 1].websock.onmessage = this.websocketonmessage
                    this.chatroom[this.chatroom.length - 1].websock.onopen = this.websocketonopen(chatroomTemp[index].chatUserID, chatroomTemp[index].chatroomID)
                    this.chatroom[this.chatroom.length - 1].websock.onerror = this.websocketonerror
                    this.chatroom[this.chatroom.length - 1].websock.onclose = this.websocketclose
                  }, 0)
                  if (!(this.userInfo.type === 'teacher' && this.$store.state.doChatAction !== 'list')) {
                    this.$store.state.doChatroomID = room.chatroomID
                    this.chatroomChatroomID = room.chatroomID
                  } else {
                    // this.updateStatus = false
                  }
                }
              })
            } else if (res.status === 'failed') {
              // this.snackbar = true
              // this.color = 'red'
              // this.errorMessages = res.errMsg
              this.$emit('snackbar', ['red', 'error:' + res.errMsg])
            } else {
              // this.snackbar = true
              // this.color = 'orange'
              // this.errorMessages = '發生錯誤，請稍後再試'
              this.$emit('snackbar', ['orange', '發生錯誤，請稍後再試'])
            }
          })
          .catch(err => {
            // this.snackbar = true
            // this.color = 'orange'
            // this.errorMessages = '發生錯誤，請稍後再試'
            this.$emit('snackbar', ['orange', '發生錯誤，請稍後再試'])
            console.log(err)
          })
        this.updateStatus = false
      } else {
        this.$store.state.dologinView = true
      }
    },
    getSystemHistoricalMessage () {
      this.$emit('snackbar', ['blue', '(system)準備取得歷史訊息'])
      if (localStorage.getItem('token')) {
        this.$emit('snackbar', ['blue', '(system)取得歷史訊息中'])
        let body = {
          userId: (this.userInfo ? this.userInfo.user_id : -1),
          user_type: this.userInfo.type
        }
        this.$http.post('/api/chat/contentAndHistoryOfSystemChatroom/', qs.stringify(body)).then(res => res.data)
          .then(res => {
            if (res.status === 'success') {
              this.$emit('snackbar', ['green', '(system)已取得歷史訊息'])
              this.chatroom = res.data
              this.doChatroomListSort()
              // this.initSystemWebSocket()
            } else if (res.status === 'failed') {
              // this.snackbar = true
              // this.color = 'red'
              // this.errorMessages = res.errMsg
              this.$emit('snackbar', ['red', '(system)error:' + res.errMsg])
            } else {
              // this.snackbar = true
              // this.color = 'orange'
              // this.errorMessages = '(system)發生錯誤，請稍後再試'
              this.$emit('snackbar', ['orange', '(system)發生錯誤，請稍後再試'])
            }
            this.getHistoricalMessage()
          })
          .catch(err => {
            // this.snackbar = true
            // this.color = 'orange'
            // this.errorMessages = '(system)發生錯誤，請稍後再試'
            this.$emit('snackbar', ['orange', '(system)發生錯誤，請稍後再試'])
            console.log(err)
            this.getHistoricalMessage()
          })
      } else {
        this.$store.state.dologinView = true
      }
    },
    getHistoricalMessage (id) {
      this.$emit('snackbar', ['blue', '準備取得歷史訊息'])
      if (localStorage.getItem('token')) {
        this.$emit('snackbar', ['blue', '取得歷史訊息中'])
        let body = {
          userId: (this.userInfo ? this.userInfo.user_id : -1),
          user_type: this.userInfo.type
        }
        this.$http.post('/api/chat/contentAndHistoryOfChatroom/', qs.stringify(body)).then(res => res.data)
          .then(res => {
            if (res.status === 'success') {
              this.$emit('snackbar', ['green', '已取得歷史訊息'])
              this.chatroom = this.chatroom.concat(res.data)
              this.doChatroomListSort()
              // // this.initWebSocket(id)
              setTimeout(() => {
                this.initWebSocket(id)
              }, 0)
              if (this.userInfo.type === 'student') {
                // this.getTeacherProfile(id)
                // this.getLessonList(id)
              }
            } else if (res.status === 'failed') {
              // this.snackbar = true
              // this.color = 'red'
              // this.errorMessages = res.errMsg
              this.$emit('snackbar', ['red', 'error:' + res.errMsg])
            } else {
              // this.snackbar = true
              // this.color = 'orange'
              // this.errorMessages = '發生錯誤，請稍後再試'
              this.$emit('snackbar', ['orange', '發生錯誤，請稍後再試'])
            }
          })
          .catch(err => {
            // this.snackbar = true
            // this.color = 'orange'
            // this.errorMessages = '發生錯誤，請稍後再試'
            this.$emit('snackbar', ['orange', '發生錯誤，請稍後再試'])
            console.log(err)
          })
      } else {
        this.$store.state.dologinView = true
      }
    },
    // initSystemWebSocket () {
    //   // this.websockSystem = []
    //   const wsuri = this.$ws + `/ws/chat/${this.userInfo ? this.userInfo.user_id : -1}_chatroom_${localStorage.getItem('systemChatroomID.replace('system', '')')}_1/`
    //   // this.websockSystem = new WebSocket(wsuri)
    //   // this.websockSystem.onmessage = this.websocketSystemOnmessage
    //   // this.websockSystem.onopen = this.websocketSystemOnopen('system', localStorage.getItem('systemChatroomID'))
    //   // this.websockSystem.onerror = this.websocketSystemOnerror
    //   // this.websockSystem.onclose = this.websocketSystemClose
    //   this.chatroom[this.chatroom.length - 1].websock = new WebSocket(wsuri)
    //   this.chatroom[this.chatroom.length - 1].websock.onmessage = this.websocketSystemOnmessage
    //   this.chatroom[this.chatroom.length - 1].websock.onopen = this.websocketSystemOnopen('system', localStorage.getItem('systemChatroomID'))
    //   this.chatroom[this.chatroom.length - 1].websock.onerror = this.websocketSystemOnerror
    //   this.chatroom[this.chatroom.length - 1].websock.onclose = this.websocketSystemClose
    //   // const wsuri = this.$ws + `/ws/chat/1_chatroom_1/`
    //   // this.websock = new WebSocket(wsuri)
    //   // this.websock.onmessage = this.websocketonmessage
    //   // this.websock.onopen = this.websocketonopen
    //   // this.websock.onerror = this.websocketonerror
    //   // this.websock.onclose = this.websocketclose
    //   // console.log(this.websock)
    // },
    initWebSocket (id) {
      // this.websock = []
      this.chatroom.forEach((room, index) => {
        if (!id || (id === room.chatroomID)) {
          if (room.chatroom_type === 'system2user') {
            const wsuri = this.$ws + `/ws/chat/${this.userInfo ? this.userInfo.user_id : -1}_chatroom_${room.chatroomID.toString().replace('system', '')}_1/`
            this.chatroom[index].websock = new WebSocket(wsuri)
            this.chatroom[index].websock.onmessage = this.websocketSystemOnmessage
            this.chatroom[index].websock.onopen = this.websocketSystemOnopen('system', room.chatroomID)
            this.chatroom[index].websock.onerror = this.websocketSystemOnerror
            this.chatroom[index].websock.onclose = this.websocketSystemClose
          } else if (room.chatroom_type === 'user2user') {
            const wsuri = this.$ws + `/ws/chat/${this.userInfo ? this.userInfo.user_id : -1}_chatroom_${room.chatroomID}_0/`
            this.chatroom[index].websock = new WebSocket(wsuri)
            this.chatroom[index].websock.onmessage = this.websocketonmessage
            this.chatroom[index].websock.onopen = this.websocketonopen(room.chatUserID, room.chatroomID)
            this.chatroom[index].websock.onerror = this.websocketonerror
            this.chatroom[index].websock.onclose = this.websocketclose
          }
          // const wsuri = this.$ws + `/ws/chat/${this.userInfo ? this.userInfo.user_id : -1}_chatroom_${room.chatroomID}_0/`
          // // this.websock.push(new WebSocket(wsuri))
          // // this.websock[index].onmessage = this.websocketonmessage
          // // this.websock[index].onopen = this.websocketonopen(room.chatUserID, room.chatroomID)
          // // this.websock[index].onerror = this.websocketonerror
          // // this.websock[index].onclose = this.websocketclose
          // this.chatroom[index].websock = new WebSocket(wsuri)
          // this.chatroom[index].websock.onmessage = this.websocketonmessage
          // this.chatroom[index].websock.onopen = this.websocketonopen(room.chatUserID, room.chatroomID)
          // this.chatroom[index].websock.onerror = this.websocketonerror
          // this.chatroom[index].websock.onclose = this.websocketclose
        }
      })
      // const wsuri = this.$ws + `/ws/chat/1_chatroom_1/`
      // this.websock = new WebSocket(wsuri)
      // this.websock.onmessage = this.websocketonmessage
      // this.websock.onopen = this.websocketonopen
      // this.websock.onerror = this.websocketonerror
      // this.websock.onclose = this.websocketclose
      // console.log(this.websock)
    },
    websocketonerror () { // 连接建立失败重连
      // this.initWebSocket()
    },
    websocketSystemOnerror () { // 连接建立失败重连
      // this.initWebSocket()
    },
    websocketonopen (chatUserID, chatroomID) { // 连接建立之后执行send方法发送数据
      this.$emit('snackbar', ['green', '與使用者' + chatUserID + '的' + chatroomID + '號聊天室已連線'])
    },
    websocketSystemOnopen (chatUserID, chatroomID) { // 连接建立之后执行send方法发送数据
      this.$emit('snackbar', ['darkmagenta', '(system)與使用者' + chatUserID + '的' + chatroomID + '號聊天室已連線'])
    },
    websocketonmessage (e) { // 数据接收
      let redata = JSON.parse(e.data)
      if (redata.messageType === 'notice_first_msg') {
        this.$emit('snackbar', ['red', '接收到異常訊息類型'])
      } else if (redata.messageType === 'update_read_msg') {
        // 已讀狀態
        this.$emit('snackbar', ['green', redata.messageType])
        this.$emit('snackbar', ['green', redata.chatroomID + '號聊天室訊息狀態已成功更新為已讀'])
      } else {
        let update = true
        this.chatroom.forEach((room, index) => {
          if (redata.chatroomID === room.chatroomID && room.chatroom_type === 'user2user') {
            update = false
            if (redata.senderID === this.userInfo.user_id) {
              let temp = -1
              this.chatroom[index].messageInfo.forEach((msg, m) => {
                if (redata.messageTempID && msg.messageTempID && redata.messageTempID === msg.messageTempID) {
                  // this.chatroom[index].messageInfo.splice(m, 1, redata)
                  temp = m
                }
              })
              if (temp > -1) {
                this.chatroom[index].messageInfo.splice(temp, 1, redata)
              } else {
                this.chatroom[index].messageInfo.push(redata)
              }
            } else {
              this.chatroom[index].messageInfo.push(redata)
            }
            // this.chatroom[index].messageInfo.push(redata)
            this.$emit('snackbar', ['blue', '已接收訊息:' + e.data])
            let roomID = this.chatroom[this.chatroomIndex] ? this.chatroom[this.chatroomIndex].chatroomID : -1
            if (roomID === redata.chatroomID && this.doChatView) {
              setTimeout(() => {
                var element = document.getElementById('messagebox')
                element.scrollTop = document.getElementById('messagebox').scrollHeight
                this.doChatroomMessageRead(this.chatroomIndex)
              }, 0)
            } else {
              this.chatroom[index].chatUnreadMessageQty = this.chatroom[index].chatUnreadMessageQty + 1
              if (this.doChatAction === 'list') {
                setTimeout(() => {
                  this.doChatroomListSort()
                }, 0)
              }
            }
          }
        })
        if (update) {
          this.doUpdateChatroomList(redata.chatroomID)
        }
      }
    },
    websocketSystemOnmessage (e) { // 数据接收
      let redata = JSON.parse(e.data)
      if (redata.messageType === 'notice_first_msg' && redata.systemCode === 'create_chatroom') {
        this.updateStatus = true
        this.$emit('snackbar', ['darkmagenta', '(system)準備建立新聊天室'])
        this.doNewChatroomConnect('(system)有新聊天請求，系統自動建立新聊天室', redata.messageText.BtnCode)
        this.$store.state.doChatView = true
        // this.$store.state.doChatView = true
        // this.$store.state.doChatAction = 'list'
        // this.$store.state.doChatroomID = redata.messageText.BtnCode
      } else if (redata.messageType === 'update_read_msg') {
        // 已讀狀態
        this.$emit('snackbar', ['green', '(system)' + redata.chatroomID + '號聊天室訊息狀態已成功更新為已讀'])
      } else {
        this.chatroom.forEach((room, index) => {
          if (redata.chatroomID === room.chatroomID && room.chatroom_type === 'system2user') {
            if (redata.senderID === this.userInfo.user_id) {
              this.chatroom[index].messageInfo.forEach((msg, m) => {
                if (redata.messageTempID && msg.messageTempID && redata.messageTempID === msg.messageTempID) {
                  this.chatroom[index].messageInfo.splice(m, 1, redata)
                }
              })
            } else {
              this.chatroom[index].messageInfo.push(redata)
            }
            // this.chatroom[index].messageInfo.push(redata)
            this.$emit('snackbar', ['blue', '(system)已接收訊息:' + e.data])
            let roomID = this.chatroom[this.chatroomIndex] ? this.chatroom[this.chatroomIndex].chatroomID : -1
            if (roomID === redata.chatroomID && this.doChatView) {
              setTimeout(() => {
                var element = document.getElementById('messagebox')
                element.scrollTop = document.getElementById('messagebox').scrollHeight
                this.doChatroomMessageRead(this.chatroomIndex)
              }, 0)
            } else {
              this.chatroom[index].chatUnreadMessageQty = this.chatroom[index].chatUnreadMessageQty + 1
              if (this.doChatAction === 'list') {
                setTimeout(() => {
                  this.doChatroomListSort()
                }, 0)
              }
            }
          }
        })
      }
    },
    websocketsend (action, data) { // 数据发送
      if (action === 'toRead') {
        let msg = {
          senderID: this.userInfo ? this.userInfo.user_id : -1,
          chatroomID: this.chatroomChatroomID,
          messageType: 'update_read_msg',
          messageText: '',
          msg_status_update: {
            messageID: data,
            messageStatus: 'read'
          },
          messageTempID: '',
          messageCreateTime: null,
          messageStatus: null
        }
        // this.websock[this.chatroomIndex].send(JSON.stringify(msg))
        this.chatroom[this.chatroomIndex].websock.send(JSON.stringify(msg))
        this.$emit('snackbar', ['red', '下列訊息發送位置:' + this.chatroom[this.chatroomIndex].websock.url])
        this.$emit('snackbar', ['green', '已發送已讀請求:' + JSON.stringify(msg)])
      } else if (this.messageText.length > 0) {
        let msg = {
          senderID: this.userInfo ? this.userInfo.user_id : -1,
          chatroomID: this.chatroomChatroomID,
          messageType: 'text_msg',
          messageText: this.messageText,
          msg_status_update: {},
          messageTempID: new Date().getTime() + (Math.floor(Math.random() * 100) + 100),
          messageCreateTime: new Date(),
          messageStatus: 'temp'
        }
        // let msgSystem = {
        //   userId: (this.userInfo ? this.userInfo.user_id : -1),
        //   chatID: localStorage.getItem('systemChatroomID'),
        //   messageType: 'text_msg',
        //   messageText: this.messageText
        // }
        // this.websock[this.chatroomIndex].send(JSON.stringify(msg))
        this.chatroom[this.chatroomIndex].websock.send(JSON.stringify(msg))
        // this.websockSystem.send(JSON.stringify(msgSystem))
        this.$emit('snackbar', ['red', '下列訊息發送位置:' + this.chatroom[this.chatroomIndex].websock.url])
        this.$emit('snackbar', ['green', '已發送訊息:' + JSON.stringify(msg)])
        this.chatroom[this.chatroomIndex].messageInfo.push(msg)
        setTimeout(() => {
          this.messageText = ''
          var element = document.getElementById('messagebox')
          element.scrollTop = document.getElementById('messagebox').scrollHeight
        }, 0)
        // this.$emit('snackbar', ['darkmagenta', '(system)已發送訊息:' + JSON.stringify(msgSystem)])
      }
    },
    websocketsendSystem (action, data) { // 数据发送
      if (action === 'toRead') {
        let msg = {
          senderID: this.userInfo ? this.userInfo.user_id : -1,
          chatroomID: this.chatroomChatroomID,
          messageType: 'update_read_msg',
          messageText: '',
          msg_status_update: {
            messageID: data,
            messageStatus: 'read'
          },
          messageTempID: '',
          messageCreateTime: null,
          messageStatus: null
        }
        // this.websockSystem.send(JSON.stringify(msg))
        this.chatroom[this.chatroomIndex].websock.send(JSON.stringify(msg))
        this.$emit('snackbar', ['red', '(system)下列訊息發送位置:' + this.chatroom[this.chatroomIndex].websock.url])
        this.$emit('snackbar', ['green', '(system)已發送已讀請求:' + JSON.stringify(msg)])
      } else if (this.messageText.length > 0) {
        let msg = {
          senderID: this.userInfo ? this.userInfo.user_id : -1,
          chatroomID: this.chatroomChatroomID,
          messageType: 'text_msg',
          messageText: this.messageText,
          msg_status_update: {},
          messageTempID: new Date().getTime() + (Math.floor(Math.random() * 100) + 100),
          messageCreateTime: new Date(),
          messageStatus: 'temp'
        }
        this.chatroom[this.chatroomIndex].websock.send(JSON.stringify(msg))
        // this.websockSystem.send(JSON.stringify(msg))
        this.$emit('snackbar', ['red', '(system)下列訊息發送位置:' + this.chatroom[this.chatroomIndex].websock.url])
        this.$emit('snackbar', ['green', '(system)已發送訊息:' + JSON.stringify(msg)])
        this.chatroom[this.chatroomIndex].messageInfo.push(msg)
        setTimeout(() => {
          this.messageText = ''
          var element = document.getElementById('messagebox')
          element.scrollTop = document.getElementById('messagebox').scrollHeight
        }, 0)
        // this.$emit('snackbar', ['darkmagenta', '(system)已發送訊息:' + JSON.stringify(msgSystem)])
      }
    },
    websocketclose () {
      this.$emit('snackbar', ['red', '已自動斷線，將自動重新連線'])
      this.initWebSocket()
    },
    websocketSystemClose () {
      this.$emit('snackbar', ['darkmagenta', '(system)已自動斷線，將自動重新連線'])
      // this.initSystemWebSocket()
      this.initWebSocket()
    },
    doChatroomMessageRead (index) {
      if (index > -1 && this.chatroom[index].websock) {
        let unreadList = []
        this.chatroom[index].messageInfo.forEach((msg, m) => {
          if (msg.messageStatus === 'unread') {
            unreadList.push(msg.messageID)
            this.chatroom[index].messageInfo[m].messageStatus = 'read'
          }
        })
        this.websocketsend('toRead', unreadList)
        this.chatroom[index].chatUnreadMessageQty = 0
        this.$emit('snackbar', ['brown', '已更新第' + index + '排序聊天室訊息為已讀'])
      }
    },
    doChatroomListSort () {
      this.chatroom.sort((a, b) => {
        return this.$moment(a.messageInfo[a.messageInfo.length - 1].messageCreateTime).format('YYYY-MM-DD HH:mm:ss') > this.$moment(b.messageInfo[b.messageInfo.length - 1].messageCreateTime).format('YYYY-MM-DD HH:mm:ss') ? 1 : -1
      })
      // console.log(this.websock)
      this.$emit('snackbar', ['cyan', '已更新聊天室排序'])
    },
    doCreateChatroom (id) {
      this.updateStatus = true
      this.$store.state.doChatroomID = -1
      this.$emit('snackbar', ['blue', '已建立新聊天室，準備更新歷史訊息'])
      // this.getHistoricalMessage(id)
      this.doNewChatroomConnect('進入不存在於列表的新聊天室，系統自動更新歷史訊息', id)
      // this.$store.state.doChatroomID = id
    },
    doUpdateChatroomList (id) {
      this.updateStatus = true
      this.$emit('snackbar', ['blue', '接收到新聊天室的訊息，準備更新歷史訊息'])
      this.doNewChatroomConnect('接收到來自新聊天室的訊息，系統自動更新歷史訊息', id)
    },
    doOpenChatWindows () {
      this.$emit('snackbar', ['green', '已開啟' + this.$store.state.doChatAction + '聊天室區域'])
    },
    doCloseChatWindows () {
      this.$emit('snackbar', ['black', '已從' + this.$store.state.doChatAction + '關閉聊天室區域'])
    },
    inChatroom (n) {
      this.$emit('snackbar', ['green', '已進入與使用者' + this.chatroom[n].chatUserID + '的' + this.chatroom[n].chatroomID + '號聊天室'])
    },
    outChatroom (n) {
      this.$emit('snackbar', ['black', '已離開與使用者' + this.chatroom[n].chatUserID + '的' + this.chatroom[n].chatroomID + '號聊天室'])
      this.doChatroomListSort()
    }
  }
}
</script>
<style lang='scss'>
$main-color: #2ac5ff;
$select-color: #3db1c3;
$label-color: #7a7a7a;
$not-select-color: #a7a7a7;
$button-color: #fdbe5d;
$chat-color: #fdbe5d;
$message-color: #fdd620;
$system-color: #955dfd;

#chatroom{
  position: fixed;
  z-index: 999;
  border-radius: 30px 30px 0 30px;
  box-shadow: 2px 1px 10px #707070;
  @media (max-width: 499px) {
    width: 100vw;
    height: calc(75vh - 80px);
    right: 0px;
    bottom: 80px;
  }
  @media (min-width: 500px) {
    width: 500px;
    height: 568px;
    right: 15px;
    bottom: 100px;
  }
  .topbox{
    width: 100%;
    height: 80px;
    background-color: $chat-color;
    padding: 0 15px;
    margin-right: 0 !important;
    margin-left: 0 !important;
    position: absolute;
    top: 0;
    border-radius: 30px 30px 0 0;
    display: flex;
    flex-flow: row;
    align-items: center;
    /*justify-content: space-between;*/
    justify-content: center;
    >span{
      font-size: 30px;
      font-weight: bold;
      letter-spacing: 0.5px;
      color: #515c6f;
      align-items: center;
      display: flex;
      justify-content: center;
      >.photo{
        border-radius: 50%;
        width: 60px;
        height: 60px;
        margin-right: 10px;
        background-color: #ffffff;
        background-size: cover;
        background-position: 50% center;
        border-radius: 50%;
      }
    }
    >i{
      font-size: 30px;
    }
    &.system {
      background-color: $system-color !important;
      >span{
        color: #ffffff !important;
      }
    }
  }
  .booking{
    position: absolute;
    top: 80px;
    background-color: #D97D54;
    color:#ffffff;
    width: 100%;
    height: 40px;
    font-size: 20px;
    display: flex;
    justify-content: center;
    align-items: center;
    >i.left{
      animation-name: booking_icon_left;
      animation-duration: 0.5s;
      animation-iteration-count:infinite;
      @keyframes booking_icon_left{
        0%{
          margin-right: 5px;
          opacity: 0.8;
        }
        50%{
          margin-right: 7px;
          opacity: 1;
        }
        100%{
          margin-right: 5px;
          opacity: 0.8;
        }
      }
    }
    >i.right{
      animation-name: booking_icon_right;
      animation-duration: 0.5s;
      animation-iteration-count:infinite;
      @keyframes booking_icon_right{
        0%{
          margin-left: 5px;
          opacity: 0.8;
        }
        50%{
          margin-left: 7px;
          opacity: 1;
        }
        100%{
          margin-left: 5px;
          opacity: 0.8;
        }
      }
    }
  }
  .middlebox{
    width: 100%;
    height: calc(100% - 80px);
    background-color:#ffffff;
    position: absolute;
    top: 80px;
    overflow: auto;
    border-radius: 0 0 0 30px;
    .loading{
    width: 100%;
    height: 40px;
    margin: 20px 0;
    }
    &.bg-message{
      height: calc(100% - 80px - 60px);
      background-color: #fffae4;
      top: 80px;
      border-radius: 0;
      &.bg-booking{
        top: 120px;
        height: calc(100% - 80px - 40px - 60px);
      }
    }
    .list-area{
      display: flex;
      flex-flow: column-reverse;
      .list{
        position: relative;
        height: 80px;
        padding-left: 85px;
        padding-right: 15px;
        border-bottom: 1px $not-select-color solid;
        display: -webkit-box;
        -webkit-line-clamp: 3;
        -webkit-box-orient: vertical;
        overflow: hidden;
        >.photo{
          position: absolute;
          top: 10px;
          left: 15px;
          border-radius: 50%;
          width: 60px;
          height: 60px;
          background-color: #ffffff;
          background-size: cover;
          background-position: 50% center;
          border-radius: 50%;
        }
        .name{
          width: calc(100% - 75px);
          display: inline-block;
        }
        .time{
          width: 75px;
          display: inline-block;
          color: $label-color;
          font-size: 10px;
          text-align: right;
        }
        >p{
          margin: 0 !important;
          line-height: 25px!important;
        }
        &:hover{
          background-color: #fffae4;
        }
        &.new{
          .name{
            font-weight: 700 !important;
          }
          >p{
            font-weight: 700 !important;
          }
        }
      }
    }
    .message{
      margin:15px 15px;
      position:relative;
      >p{
        margin-bottom: 0;
        font-size: 16px !important;
        text-align: justify;
        display: inline-block;
        max-width: 350px;
        padding: 10px;
        box-shadow: 2px 1px 5px #cacaca;
      }
      .time{
        display: none;
        color: $label-color;
        font-size: 10px;
        margin: 0 5px;
      }
      &.our{
        text-align: right;
        >p{
          background: linear-gradient(-45deg, ($select-color - 40) 0%, $select-color 50%);
          border-radius: 10px 10px 0 10px;
          color: #ffffff;
          &>span{
            color: #ffffff;
            &>a{
              color: #ffffff !important;
              text-decoration: underline;
              font-weight: 700 !important;
            }
          }
        }
        .time:nth-of-type(1){
          display: inline-block;
          bottom: -10px;
          position: inherit;
        }
      }
      &.other{
