<script>
import qs from 'qs'
import { mapState } from 'vuex'

export default {
  data: () => ({
    // links: [
    //   { name: 'Home', Link: '/dashboard' },
    //   { name: 'Demo', Link: 'https://clintoxx.github.io/vuetify-admin-dashboard/' },
    //   { name: 'GitHub', Link: 'https://github.com/ClintOxx' }
    // ]
    snackbar: false,
    color: 'red',
    errorMessages: ''
  }),
  computed: {
    ...mapState(['appName', 'doChatView', 'doChatAction', 'doChatroomID']),
    userInfo () {
      return JSON.parse(localStorage.getItem('user'))
    }
  },
  methods: {
    test () {
      alert('XXX')
    },
    doConnectSystemChatroom () {
      if (localStorage.getItem('token')) {
        let body = {
          userID: this.userInfo ? this.userInfo.user_id : -1
        }
        this.$http.post('/api/chat/checkSystemChatroom/', qs.stringify(body)).then(res => res.data)
          .then(res => {
            if (res.status === 'success') {
              // this.snackbar = true
              // this.color = 'green'
              // this.errorMessages = '已開啟聊天室'
              this.$store.state.doChatView = true
              // this.$store.state.doChatAction = 'message'
              this.$store.state.doChatroomID = res.data.chatroomID
            } else if (res.status === 'failed') {
              this.snackbar = true
              this.color = 'red'
              this.errorMessages = res.errMsg
            } else {
              this.snackbar = true
              this.color = 'orange'
              this.errorMessages = '發生錯誤，請稍後再試'
            }
          })
          .catch(err => {
            this.snackbar = true
            this.color = 'orange'
            this.errorMessages = '發生錯誤，請稍後再試'
            console.log(err)
          })
      } else {
        this.$store.state.dologinView = true
      }
    }
  }
}
</script>
