          // this.snackbar = true
          // this.color = 'orange'
          // this.errorMessages = '發生錯誤，請稍後再試'
          console.log(err)
        })
    },
    doFavorite (id, status) {
      if (localStorage.getItem('token')) {
        let body = {
          userId: (this.userInfo ? this.userInfo.user_id : -1),
          lessonID: id,
          status: status
        }
        this.$http.post('/api/lesson/addOrRemoveFavoriteLessons/', qs.stringify(body)).then(res => res.data)
          .then(res => {
            if (res.status === 'success') {
              // this.getLessonRecommend()
              this.resLessonRecommendList.forEach((lesson, index) => {
                if (lesson.lessonID === id) {
                  lesson.is_this_my_favorite_lesson = status
                }
              })
              this.snackbar = true
              this.color = 'green'
              this.errorMessages = '已' + (status ? '加入' : '移除') + '收藏'
            } else if (res.status === 'failed') {
              this.snackbar = true
              this.color = 'red'
              this.errorMessages = res.errMsg
            } else {
              this.snackbar = true
              this.color = 'orange'
              this.errorMessages = '發生錯誤，請稍後再試'
            }
          })
          .catch(err => {
            this.snackbar = true
            this.color = 'orange'
            this.errorMessages = '發生錯誤，請稍後再試'
            console.log(err)
          })
      } else {
        this.$store.state.dologinView = true
      }
    }
  }
}
</script>
