<template>
  <v-content>
    <v-dialog
      v-model="loadingView"
      persistent
      >
      <p style="letter-spacing: 5px;text-align: center !important;color: #ffffff;background-color: #000000dd;padding: 15px;margin: auto;">
      <img
      :src="loading"
      style="width: 2rem;margin-bottom: 10px;">
      <br>
      {{ loadingViewText }}
      </p>
    </v-dialog>
    <v-snackbar
      v-model="snackbar"
      :top="true"
      :color="color">
      {{ errorMessages }}
      <v-btn
        dark
        flat
        @click="snackbar = false">
        Close
      </v-btn>
    </v-snackbar>
  </v-content>
</template>

<script>
import qs from 'qs'
import loading from '@/assets/img/loading.gif'
import { mapState } from 'vuex'
export default {
  name: 'QuestionBankSellProgram',
  metaInfo () {
    return {
      title: this.appName + '｜題庫預購'
    }
  },
  data: function () {
    return {
      loadingView: false,
      loadingViewText: '處理中',
      loading,
      snackbar: false,
      color: 'red',
      errorMessages: ''
    }
  },
  computed: {
    ...mapState(['root', 'appName', 'cartItem']),
    userInfo () {
      return JSON.parse(localStorage.getItem('user'))
    },
    shoppingInfo () {
      return JSON.parse(localStorage.getItem('shopping'))
    },
    isTeacherCB () {
      if (this.userInfo) {
        if (this.userInfo.group.indexOf('test_teacher') > -1) {
          return true
        } else {
          return false
        }
      } else {
        return false
      }
    },
    isStudentCB () {
      if (this.userInfo) {
        if (this.userInfo.group.indexOf('test_student') > -1) {
          return true
        } else {
          return false
        }
      } else {
        return false
      }
    }
  },
  watch: {
  },
  // Sends action to Vuex that will log you in and redirect to the dash otherwise, error
  mounted () {
    let publicPage = true
    if (localStorage.getItem('token') || publicPage) {
      let body = {
        userId: (this.userInfo ? this.userInfo.user_id : -1),
        // url: this.$route.path
        url: '/home'
      }
      this.$http.post('/api/account/authCheck/', qs.stringify(body)).then(res => res.data)
        .then(res => {
          if (res.status === 'success') {
            if (res.data.authority) {
              if (this.$route.params.promotion_code === 'pre_order') {
                this.loadingViewText = '正在為您轉跳到結帳頁'
                this.loadingView = true
                this.addShoppingItem('exam_bank_order', '365', 3680, 'pre_order')
              }
            } else {
              // this.$router.push('/')
            }
          } else if (localStorage.getItem('user')) {
            this.$store.dispatch('logout').then(() => {
              this.$router.go(0)
            })
          }
        })
        .catch(err => {
          console.log(err)
          if (localStorage.getItem('user')) {
            this.$store.dispatch('logout').then(() => {
              this.$router.go(0)
            })
          }
        })
    } else {
      this.$router.push('/')
    }
  },
  methods: {
    test () {
      alert('未開放')
    },
    addShoppingItem (productType, productSaleCase, productSalePrice, productPromotionCode) {
      // let shopping = {}
      // if (localStorage.getItem('shopping')) {
      //   shopping = JSON.parse(localStorage.getItem('shopping'))
      // } else {
      //   shopping = {
      //     updateTime: new Date().getTime(),
      //     item: []
      //   }
      // }
      let shopping = JSON.parse(localStorage.getItem('shopping')) || { updateTime: new Date().getTime(), item: [] }
      let preOrder = false
      shopping.item.forEach((item, index) => {
        if (item.order_type === 'exam_bank_order') {
          preOrder = true
        }
      })
      if (!preOrder) {
        shopping.updateTime = new Date().getTime()
        shopping.item.push({ order_type: productType, userID: (this.userInfo ? this.userInfo.user_id : -1), sales_set: productSaleCase, total_amount_of_the_sales_set: productSalePrice, q_discount: 0, code_discount: 0, code: productPromotionCode })
        localStorage.setItem('shopping', JSON.stringify(shopping))
        this.$store.state.cartItem = shopping.item.length
      }
      setTimeout(() => {
        this.$router.push('/store/checkout')
      }, 750)
    }
  }
}
</script>
