<template>
  <div
    id="blog-main"
    class="container my-0 p-0">
    <div v-html="resBlogData"/>
    <v-dialog
      v-model="loadingView"
      persistent
      >
      <p style="letter-spacing: 5px;text-align: center !important;color: #ffffff;background-color: #000000dd;padding: 15px;margin: auto;">
      <img
      :src="loading"
      style="width: 2rem;margin-bottom: 10px;">
      <br>
      {{ loadingViewText }}
      </p>
    </v-dialog>
  </div>
</template>

<script>
// import $ from 'jquery'
import qs from 'qs'
import loading from '@/assets/img/loading.gif'
import { mapState } from 'vuex'

export default {
  name: 'BlogMain',
  metaInfo () {
    return {
      title: this.appName + '｜Blog',
      meta: [
        {
          name: 'description',
          content: this.appName + '｜最優質的家教平台｜免費媒合｜智慧分析學習成效' // to do
        },
        {
          property: 'og:title',
          content: this.appName + '｜Blog'
        },
        {
          property: 'og:description',
          content: this.appName + '｜最優質的家教平台｜免費媒合｜智慧分析學習成效' // to do
        },
        {
          property: 'og:type',
          content: 'website'
        },
        {
          property: 'og:image',
          content: this.root + 'user_upload/main/snapshot.jpeg'
        }
      ]
    }
  },
  data: () => ({
    // page: 1,
    loadingViewText: '處理中',
    loadingView: false,
    loading,
    resBlogData: ''
  }),
  computed: {
    ...mapState(['root', 'appName', 'isProduction']),
    userInfo () {
      return JSON.parse(localStorage.getItem('user'))
    }
  },
  watch: {
    '$route.fullPath': function (newValue) {
      this.doMounted()
    }
  },
  mounted () {
    this.loadingViewText = '文章載入中'
    this.loadingView = true
    this.doMounted()
    setTimeout(() => {
      if (this.resBlogData === '') {
        this.getBlog()
      }
    }, 3000)
  },
  methods: {
    doMounted () {
      let publicPage = true
      if (localStorage.getItem('token') || publicPage) {
        let body = {
          userId: (this.userInfo ? this.userInfo.user_id : -1),
          url: this.$route.path
        }
        this.$http.post('/api/account/authCheck/', qs.stringify(body)).then(res => res.data)
          .then(res => {
            // alert('6')
            if (res.status === 'success') {
              if (res.data.authority) {
                // alert('7')
                this.getBlog()
                // if (this.$route.params.page) {
                //   this.page = this.$route.params.page
                // }
                // this.$http.get('/articles/').then(res => res.data)
                //   .then(res => {
                //     this.resBlogData = res
                //     // setTimeout(() => {
                //     //   if (this.isProduction) {
                //     //     this.doGtagsEvent('Blog-主頁：' + this.pageName, '瀏覽Blog主頁', '瀏覽Blog主頁')
                //     //   }
                //     // }, 0)
                //   })
              } else {
                // this.$router.push('/')
              }
            } else if (localStorage.getItem('user')) {
              this.$store.dispatch('logout').then(() => {
                this.$router.go(0)
              })
            }
          })
          .catch(err => {
            alert('9--' + err)
            console.log(err)
            if (localStorage.getItem('user')) {
              this.$store.dispatch('logout').then(() => {
                this.$router.go(0)
              })
            }
          })
      } else {
        alert('99')
        // this.$router.push('/')
      }
    },
    getBlog () {
      this.$http.get(`/articles/`).then(res => res.data)
        .then(res => {
          this.loadingView = false
          // alert('8--' + res)
          this.resBlogData = res
          setTimeout(() => {
            // setTimeout(() => {
            //   if (this.isProduction) {
            //     this.doGtagsEvent('Blog-文章：' + this.pageName, '瀏覽Blog文章[' + this.pageName + ']', document.getElementsByClassName('card-title')[0].innerHTML)
            //   }
            // }, 0)
          }, 0)
        })
    },
    doGtagsEvent (name, description, remarks) {
      if (this.isProduction) {
        this.$gtag.event(name, {
          description: description,
          remarks: remarks,
          page_path: this.$route.fullPath,
          hostname: this.root,
          user_id: this.userInfo ? this.userInfo.user_id : -1
        })
      }
    }
  }
}
</script>
