<script>
import qs from 'qs'
import boyTeacher from '@/assets/img/boy_teacher.jpg'
import girlTeacher from '@/assets/img/girl_teacher.jpg'
import boyStudent from '@/assets/img/boy_student.jpg'
import girlStudent from '@/assets/img/girl_student.jpg'
import cardBg1 from '@/assets/img/lesson_card_bg1.jpg'
import cardBg2 from '@/assets/img/lesson_card_bg2.jpg'
import cardBg3 from '@/assets/img/lesson_card_bg3.jpg'
import chatIconSmall from '@/assets/img/chat_icon_small.png'
import loading from '@/assets/img/loading.gif'
import { mapState } from 'vuex'
export default {
  name: 'LessonReservation',
  metaInfo () {
    return {
      title: this.appName + '｜預約管理'
    }
  },
  data: function () {
    return {
      loadingView: false,
      loadingViewText: '處理中',
      pageView: false,
      status: 'to_be_confirmed',
      filterCondition: '',
      keyword: '',
      timeStart: '',
      timeEnd: '',
      filter: false,
      finishTimeView: false,
      finishTimeStartView: false,
      finishTimeEndView: false,
      finishTimeStart: '',
      finishTimeEnd: '',
      writeReviewsView: false,
      readReviewsView: false,
      writeReviewsScoreMove: 0,
      writeReviewsInfo: {
        lesson_booking_info_id: 0,
        score_given: 0,
        remark_given: '',
        is_student_late_for_lesson: false,
        is_student_frivolous_in_lesson: false,
        is_student_or_parents_not_friendly: false
      },
      readReviewsPhoto: '',
      readReviewsInfo: {
        score_given_to_teacher: -1,
        remark_given_to_teacher: '',
        is_teacher_late_for_lesson: false,
        is_teacher_frivolous_in_lesson: false,
        is_teacher_incapabale: false,
        score_given_to_student: -1,
        remark_given_to_student: '',
        is_student_late_for_lesson: false,
        is_student_frivolous_in_lesson: false,
        is_student_or_parents_not_friendly: false
      },
      boyTeacher,
      girlTeacher,
      boyStudent,
      girlStudent,
      cardBg1,
      cardBg2,
      cardBg3,
      chatIconSmall,
      loading,
      loadingStatus: true,
      snackbar: false,
      color: 'red',
      errorMessages: '',
      weekList: ['日', '一', '二', '三', '四', '五', '六'],
      resLessonReservationDate: []
    }
  },
  computed: {
    ...mapState(['root', 'snapshot', 'appName', 'dologinView', 'doChatView', 'doChatAction', 'doChatroomID']),
    userInfo () {
      return JSON.parse(localStorage.getItem('user'))
    }
  },
  // Sends action to Vuex that will log you in and redirect to the dash otherwise, error
  watch: {
    status (val) {
      this.getTeachersBookingHistory()
    },
    'writeReviewsInfo.lesson_booking_info_id' (val) {
      this.writeReviewsInfo.score_given = 0
      this.writeReviewsInfo.remark_given = ''
      this.writeReviewsInfo.is_student_late_for_lesson = false
      this.writeReviewsInfo.is_student_frivolous_in_lesson = false
      this.writeReviewsInfo.is_student_or_parents_not_friendly = false
    },
    dologinView (val) {
      if (!val) {
        this.$router.push('/')
      }
    }
  },
  mounted () {
    if (localStorage.getItem('token') && this.userInfo.type === 'teacher') {
      let body = {
        userId: (this.userInfo ? this.userInfo.user_id : -1),
        url: this.$route.path
      }
      this.$http.post('/api/account/authCheck/', qs.stringify(body)).then(res => res.data)
        .then(res => {
          if (res.status === 'success') {
            if (res.data.authority) {
              this.getTeachersBookingHistory()
            } else {
              this.$router.push('/')
            }
          } else if (localStorage.getItem('user')) {
            this.$store.dispatch('logout').then(() => {
              this.$router.go(0)
            })
          }
        })
        .catch(err => {
          console.log(err)
          if (localStorage.getItem('user')) {
            this.$store.dispatch('logout').then(() => {
              this.$router.go(0)
            })
          }
        })
    } else if (!localStorage.getItem('token')) {
      this.$store.state.dologinView = true
    } else {
      this.$router.push('/')
    }
  },
  methods: {
    test () {
      alert('未開放')
    },
    getTeachersBookingHistory () {
      this.resLessonReservationDate = []
      this.loadingStatus = true
      let body = {
        userId: (this.userInfo ? this.userInfo.user_id : -1),
        searched_by: this.keyword,
        filtered_by: this.status,
        registered_from_date: this.timeStart,
        registered_to_date: this.timeEnd
      }
      this.$http.post('/api/lesson/getTeachersBookingHistory/', qs.stringify(body)).then(res => res.data)
        .then(res => {
          this.loadingStatus = false
          if (res.status === 'success') {
            this.filter = false
            if (this.userInfo.type === 'teacher') {
              this.pageView = true
              if (this.keyword !== '' || this.timeStart !== '' || this.timeEnd !== '') {
                this.filterCondition = '以下為'
                if (this.timeStart !== '' || this.timeEnd !== '') {
                  this.filterCondition = this.filterCondition + this.timeStart + '至' + this.timeEnd
                }
                if (this.keyword !== '') {
                  this.filterCondition = this.filterCondition + '符合"' + this.keyword + '"'
                }
                this.filterCondition = this.filterCondition + '的篩選結果'
              } else {
                this.filterCondition = ''
              }
              this.resLessonReservationDate = res.data
            } else {
              this.$router.push('/')
            }
          } else if (res.status === 'failed') {
            this.snackbar = true
            this.color = 'red'
            this.errorMessages = res.errMsg
          } else {
            this.snackbar = true
            this.color = 'orange'
            this.errorMessages = '發生錯誤，請稍後再試'
          }
        })
        .catch(err => {
          this.snackbar = true
          this.color = 'orange'
          this.errorMessages = '發生錯誤，請稍後再試'
          console.log(err)
        })
    },
    getReadReviewsOfCertainLessons (id, path, male, type) {
      this.loadingStatus = true
      let body = {
        userId: (this.userInfo ? this.userInfo.user_id : -1),
        type: this.userInfo ? this.userInfo.type : '',
        lesson_booking_info_id: id
      }
      this.$http.post('/api/lesson/readReviewsOfCertainLessons/', qs.stringify(body)).then(res => res.data)
        .then(res => {
          this.loadingStatus = false
          if (res.status === 'success') {
            if (path === '' || path === null) {
              if (type === 'teacher') {
                if (male) {
                  this.readReviewsPhoto = boyTeacher
                } else {
                  this.readReviewsPhoto = girlTeacher
                }
              } else if (type === 'student') {
                if (male) {
                  this.readReviewsPhoto = boyStudent
                } else {
                  this.readReviewsPhoto = girlStudent
                }
              }
            } else {
              this.readReviewsPhoto = path
            }
            this.readReviewsInfo = res.data
            this.readReviewsView = true
          } else if (res.status === 'failed') {
            this.snackbar = true
            this.color = 'red'
            this.errorMessages = res.errMsg
          } else {
            this.snackbar = true
            this.color = 'orange'
            this.errorMessages = '發生錯誤，請稍後再試'
          }
        })
        .catch(err => {
          this.snackbar = true
          this.color = 'orange'
          this.errorMessages = '發生錯誤，請稍後再試'
          console.log(err)
        })
    },
    doTimeConversion (time) {
      let start = ((Number(time[0]) < 20) ? '0' : '') + parseInt(Number(time[0]) / 2) + ':' + ((Number(time[0]) % 2) ? '30' : '00')
      let end = (((Number(time[time.length - 1]) + 1) < 20) ? '0' : '') + parseInt((Number(time[time.length - 1]) + 1) / 2) + ':' + (((Number(time[time.length - 1]) + 1) % 2) ? '30' : '00')
      return start + '-' + end
    },
    finishTime (id, date, time) {
      if (confirm('請確認是否已完成課程，結算後將無法進入課程！\n請問您是否要繼續？')) {
        this.finishLessonID = id
        this.finishLessonDate = date
        this.finishTimeStart = ((Number(time[0]) < 20) ? '0' : '') + parseInt(Number(time[0]) / 2) + ':' + ((Number(time[0]) % 2) ? '30' : '00')
        this.finishTimeEnd = (((Number(time[time.length - 1]) + 1) < 20) ? '0' : '') + parseInt((Number(time[time.length - 1]) + 1) / 2) + ':' + (((Number(time[time.length - 1]) + 1) % 2) ? '30' : '00')
        this.finishTimeView = true
      }
    },
    doCompletedTime () {
      if (this.finishTimeStart.split(':')[1] % 10 === 0 && this.finishTimeEnd.split(':')[1] % 10 === 0) {
        let minutes = (new Date(this.$moment(new Date()).format('YYYY/MM/DD') + ' ' + this.finishTimeEnd).getTime() - new Date(this.$moment(new Date()).format('YYYY/MM/DD') + ' ' + this.finishTimeStart).getTime()) / 1000 / 60
        if (minutes >= 10) {
          if (confirm('送出時數後將交由學生進行核對\n請問您是否要繼續？')) {
            this.finishTimeView = false
            this.loadingViewText = '送出時數中'
            this.loadingView = true
            let body = {
              userId: (this.userInfo ? this.userInfo.user_id : -1),
              lesson_booking_info_id: this.finishLessonID,
              lesson_date: this.finishLessonDate,
              // start_hour: 0,
              // start_minute: 0,
              start_time: this.finishTimeStart,
              // end_hour: 0,
              // end_minute: 0,
              end_time: this.finishTimeEnd,
              time_interval_in_minutes: minutes
            }
            this.$http.post('/api/lesson/lessonCompletedNotificationFromTeacher/', qs.stringify(body)).then(res => res.data)
              .then(res => {
                this.loadingView = false
                if (res.status === 'success') {
                  this.getTeachersBookingHistory()
                  // this.finishTimeView = false
                  this.finishLessonID = ''
                  this.finishLessonDate = ''
                  this.finishTimeStart = ''
                  this.finishTimeEnd = ''
                  this.snackbar = true
                  this.color = 'green'
                  this.errorMessages = '時數已送出並已通知學生確認時數'
                } else if (res.status === 'failed') {
                  this.finishTimeView = true
                  this.snackbar = true
                  this.color = 'red'
                  this.errorMessages = res.errMsg
                } else {
                  this.finishTimeView = true
                  this.snackbar = true
                  this.color = 'orange'
                  this.errorMessages = '發生錯誤，請稍後再試'
                }
              })
              .catch(err => {
                this.loadingView = false
                this.finishTimeView = true
                this.snackbar = true
                this.color = 'orange'
                this.errorMessages = '發生錯誤，請稍後再試'
                console.log(err)
              })
          }
        } else {
          this.snackbar = true
          this.color = 'red'
          this.errorMessages = '時數異常，請再次確認'
        }
      } else {
        this.snackbar = true
        this.color = 'red'
        this.errorMessages = '開始時間及結束時間最小單位須為10分鐘'
      }
    },
    doChangingStatus (id, status, text) {
      if (confirm('你確定要' + text + '預約嗎?')) {
        this.loadingViewText = text + '預約中'
        this.loadingView = true
        let body = {
          userId: (this.userInfo ? this.userInfo.user_id : -1),
          bookingID: id,
          bookingStatus: status
        }
        this.$http.post('/api/lesson/changingLessonBookingStatus/', qs.stringify(body)).then(res => res.data)
          .then(res => {
            this.loadingView = false
            if (res.status === 'success') {
              this.getTeachersBookingHistory()
              this.snackbar = true
              this.color = 'green'
              this.errorMessages = '已成功' + text + '預約'
            } else if (res.status === 'failed') {
              this.snackbar = true
              this.color = 'red'
              this.errorMessages = res.errMsg
            } else {
              this.snackbar = true
              this.color = 'orange'
              this.errorMessages = '發生錯誤，請稍後再試'
            }
          })
          .catch(err => {
            this.loadingView = false
            this.snackbar = true
            this.color = 'orange'
            this.errorMessages = '發生錯誤，請稍後再試'
            console.log(err)
          })
      }
    },
    doTeacherWriteStudentReviews () {
      if (this.writeReviewsInfo.score_given > 0) {
        if (confirm('你確定要送出評價嗎?')) {
          this.writeReviewsView = false
          this.loadingViewText = '送出評價中'
          this.loadingView = true
          let body = {
            userId: (this.userInfo ? this.userInfo.user_id : -1),
            lesson_booking_info_id: this.writeReviewsInfo.lesson_booking_info_id,
            score_given: this.writeReviewsInfo.score_given,
            remark_given: this.writeReviewsInfo.remark_given,
            is_student_late_for_lesson: this.writeReviewsInfo.is_student_late_for_lesson,
            is_student_frivolous_in_lesson: this.writeReviewsInfo.is_student_frivolous_in_lesson,
            is_student_or_parents_not_friendly: this.writeReviewsInfo.is_student_or_parents_not_friendly
          }
          this.$http.post('/api/lesson/teacherWriteStudentReviews/', qs.stringify(body)).then(res => res.data)
            .then(res => {
              this.loadingView = false
              if (res.status === 'success') {
                this.writeReviewsInfo.lesson_booking_info_id = 0
                this.getTeachersBookingHistory()
                this.snackbar = true
                this.color = 'green'
                this.errorMessages = '已成功送出評價'
              } else if (res.status === 'failed') {
                this.writeReviewsView = true
                this.snackbar = true
                this.color = 'red'
                this.errorMessages = res.errMsg
              } else {
                this.writeReviewsView = true
                this.snackbar = true
                this.color = 'orange'
                this.errorMessages = '發生錯誤，請稍後再試'
              }
            })
            .catch(err => {
              this.loadingView = false
              this.writeReviewsView = true
              this.snackbar = true
              this.color = 'orange'
              this.errorMessages = '發生錯誤，請稍後再試'
              console.log(err)
            })
        }
      } else {
        this.snackbar = true
        this.color = 'red'
        this.errorMessages = '尚未填寫評分'
      }
    },
    doConnectChatroom (id) {
      if (localStorage.getItem('token')) {
        let body = {
          userId: (this.userInfo ? this.userInfo.user_id : -1),
          chatUserID: id
        }
        this.$http.post('/api/chat/checkOrCreateChatroom/', qs.stringify(body)).then(res => res.data)
          .then(res => {
            if (res.status === 'success') {
              // this.snackbar = true
              // this.color = 'green'
              // this.errorMessages = '已開啟聊天室'
              this.$store.state.doChatView = true
              // this.$store.state.doChatAction = 'message'
              this.$store.state.doChatroomID = res.data.chatroomID
            } else if (res.status === 'failed') {
              this.snackbar = true
              this.color = 'red'
              this.errorMessages = res.errMsg
            } else {
              this.snackbar = true
              this.color = 'orange'
              this.errorMessages = '發生錯誤，請稍後再試'
            }
          })
          .catch(err => {
            this.snackbar = true
            this.color = 'orange'
            this.errorMessages = '發生錯誤，請稍後再試'
            console.log(err)
          })
      } else {
        this.$store.state.dologinView = true
      }
    }
  }
}
</script>
